<template>
    <div id="Multiplefill">
        <el-card shadow="hover">
            <div slot="header" class="clearfix">
              <span >题目{{bean+1}}</span>
            </div>
            <el-row>   
                <el-col :span="22"> 
                    <el-input placeholder="多项填空题的题目" clearable autosize v-model="QuestionList.questions[bean].title"></el-input>
                </el-col>
                <el-col :span="1" :offset="1">
                   <el-button type="text" class='fa fa-trash-o fa-2x'  @click="delete_all"></el-button>
                </el-col>
            </el-row>
            <el-row v-for="(item,key) in QuestionList.questions[bean].choices" :key="key">
                <Space  @deletive="deleting" :beanPrev="bean" :QuestionList.sync="QuestionList" :bean="key" :isSend="isSend" :optionList.sync="question"></Space>
            </el-row>
            <el-row>
                <el-button type="text" icon="el-icon-plus" @click="addSinglefill">添加单个填空项</el-button>
            </el-row> 
        </el-card>
    </div>
</template>
<script>
import Space from '@/components/Questionnaire/Space'
export default {
    props:{
        bean:{

        },
        isSend:{

        },
        QuestionList:{

        },
    },
    components:{
        Space,
    },
    data(){
        return{
            items:[1,1],
            question:{
              "serialNumber":1,     //题目序号                              没有发送0过来
              "title":"题目标题", //题目标题
              "answerType":2,       //单选：0，多选：1，文字题：2，打分题：3
              "optionType":0,       //必选：0，可选：1
              "minNumber":0,        //最少选项数（多选）、最少字数（文字题）    没有发送0过来
              "maxNumber":0,        //最多选项数（多选）、最少字数（文字题）    没有发送0过来
              "page":0,              //页数，不分页发0
              //"precedentChoiceId":1,//关联选项id,                            **没有不要发送过来**
              //"precedentQuestionId":1, //关联题目id                          **没有不要发送过来**
              "choices":            //选项
              [
                   
              ]
              },
        }

    },
    methods:{
        deleting(index){
            //this.items.splice(index,1);   //删除哪一项 
            //delete this.items[index];
            this.QuestionList.questions[this.bean].choices.splice(index,1);
        },
        delete_all(){   //删除整道题目
            this.$emit('delete_big',this.bean);
        },
        addSinglefill(){
            //this.items.splice(this.items.length-1,0,1) ;
            this.QuestionList.questions[this.bean].choices.push({
                "serialNumber":1,         //序号                        没有发送0过来
                "title":"选项标题",   //标题
                "type":1,                  //选项型:0、文字型:1
                "defaultStatus":0         //默认状态                      没选就发0  
            })
        }
    },
    watch:{
      isSend:function(){
        /* this.question.serialNumber = this.bean+1;
        this.question.maxNumber = this.items.length;    //控制最小选项数
        this.question.minNumber = this.items.length;    //控制最多选项数
        this.QuestionList.questions.push(this.question); */
    }
}
}
</script>
<style scoped lang='scss'>
 .el-card{
    padding:25px;
  }
.el-input{
    margin:10px 0;
  }
</style>


