<template>
  <div id="person-list">
    <!-- <ul class="title">
      <li class="menu_title">
        <slot name="title">
        </slot>
      </li>
      <li class="list_title" v-for=""></li>
    </ul> -->
    <div class="slide">
      <slide :my-message="myMessage" :current-tab="currentTab" :isRequested="this.isRequested"></slide>
    </div>
    <div class="ul_list">
      <p v-for="(item,index) in items" class="perpon-name" v-if="index<9" @click="routerLink(index,currentTab)">
        {{item.name}}
      </p>
    </div>
  </div>
</template>

<script>
import Slide from "@/components/common/slide";

export default {
  name: "person-list",
  data() {
    return {
      items: [
        { name: "" },
      ]
    };
  },
  props: ["myMessage", "currentTab","isRequested"],
  methods: {
    init() {
      if (this.myMessage) {
        //   if (this.currentTab.list == "协会会员") {
        this.items = this.myMessage;
        // }
        // if (this.currentTab.list == "高校") {
        //   this.items = [];
        //   for (let i in this.myMessage) {
        //     this.items.push(new Object());
        //     this.items[i].id = this.myMessage[i].id;
        //     this.items[i].name = this.myMessage[i].name;
        //     this.items[i].introduction = this.myMessage[i].introduction;
        // }
      }
    },
    routerLink(index, data) {
      console.log(this.currentTab.list)
      let address;
      if (this.currentTab.list == "协会会员") {
        address = "/association/vip-detail";
      }
      if (this.currentTab.list == "产品库") {
        address = "/product/product-detail";
      }
      let id = this.items[index].id;
      let item = JSON.stringify(data);
      this.$router.push({ path: address, query: { id: id, data: item } });
    }
  },
  components: {
    Slide
  },
  mounted() {
    this.init();
  },
  activated() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#person-list {
  @include max;
}
.slide {
  width: 350px;
  float: left;
  height: 250px;
  overflow: hidden;
}
.ul_list {
  width: 440px;
  float: right;
  box-sizing: border-box;
}
.perpon-name {
  // display: inline-block;
  width: 400px;
  margin: 0 auto;
  padding: 4px 10px;
  text-align: center;
  color: #7b7b7b;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.perpon-name:hover {
  color: #2e2d2d;
}
</style>



