<template>
    <div id="Multiplechoice">
        <el-card shadow="hover">
          <div slot="header" class="clearfix">
              <span>题目{{bean+1}}</span>
          </div>
          <el-row>
            <el-col :span="22">
            <el-input placeholder="多选题的题目" clearable autosize v-model="QuestionList.questions[bean].title">
            </el-input>
            </el-col>
            <el-col :span="1" :offset="1">
                   <el-button type="text" class='fa fa-trash-o fa-2x'  @click="delete_all"></el-button>
            </el-col>
          </el-row> 
          <el-row v-for="(item,key) in QuestionList.questions[bean].choices" :key="key">
            <Option @deletive="deleting"  :beanPrev="bean" :QuestionList.sync="QuestionList"  :bean="key" :isSend="isSend" :optionList.sync="question">
            </Option>
          </el-row>
          <el-row>
            <el-button type="text" icon="el-icon-plus" @click="addSinglechoice">添加单个选项</el-button>
          </el-row> 
        </el-card>
    </div>
</template>
<script>
import Option from '@/components/Questionnaire/Question-option'
export default {
    props:{
        bean:{

        },
        isSend:{

        },
        QuestionList:{

        }
    },    
    components:{
    Option
  },
data(){
  return{
    items:[1,1],
    question:{
              "serialNumber":1,     //题目序号                              没有发送0过来
              "title":"题目标题", //题目标题
              "answerType":1,       //单选：0，多选：1，文字题：2，打分题：3
              "optionType":0,       //必选：0，可选：1
              "minNumber":2,        //最少选项数（多选）、最少字数（文字题）    没有发送0过来
              "maxNumber":0,        //最多选项数（多选）、最少字数（文字题）    没有发送0过来
              "page":0,              //页数，不分页发0
              //"precedentChoiceId":1,//关联选项id,                            **没有不要发送过来**
              //"precedentQuestionId":1, //关联题目id                          **没有不要发送过来**
              "choices":            //选项
              [
                   
              ]
              },
  }
},
methods:{
deleting(index){   //子组件触发的删除单项选项的方法
    //this.items.splice(index,1);    //数组方法删除那一项
    this.QuestionList.questions[bean].choices.splice(index,1); 
  },
  delete_all(){   //删除整道题目
  //console.log(this.bean)
        this.$emit('delete_big',this.bean)
  },
  addSinglechoice(){    //增加单个选项
    //this.items.splice(this.items.length-1,0,1) ;
    this.QuestionList.questions[bean].choices.push({
                                                        "serialNumber":1,         //序号                        没有发送0过来
                                                        "title":"选项标题",   //标题
                                                        "type":0,                  //选项型:0、文字型:1
                                                        "defaultStatus":0         //默认状态                      没选就发0
                                                    })
  }
},
watch:{
  isSend:function(){
    /* this.question.serialNumber = this.bean+1;
    this.question.maxNumber = this.items.length;
    this.QuestionList.questions.push(this.question); */
  }
}
}
</script>
<style scoped lang='scss'>
.el-input{
    margin:10px 0;
  }
  .el-card{
    padding:25px;
  }
</style>


