<template>
<div id="QuestionnareList" v-loading="isLoading"    element-loading-text="问卷创建中">
    <el-row :gutter="5" class="center_row">
        <!-- <el-col  :span="3">
            <el-dropdown>
                <el-button type="primary">会议类型<i class="el-icon-arrow-down el-icon--right"></i></el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>会议类型一</el-dropdown-item>
                    <el-dropdown-item>会议类型二</el-dropdown-item>
                    <el-dropdown-item>会议类型三</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </el-col>
        <el-col  :span="3">
            <el-dropdown>
                <el-button type="primary">会议时间<i class="el-icon-arrow-down el-icon--right"></i></el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>会议时间一</el-dropdown-item>
                    <el-dropdown-item>会议时间二</el-dropdown-item>
                    <el-dropdown-item>会议时间三</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </el-col> -->
        <el-col :span="3">
            <el-button type="primary" class="addSurvey" icon="el-icon-plus" @click="addSurvey();routerLink(pathItems[0].path,1)" >新建问卷</el-button>
        </el-col>
        <el-col :span="3">
            <el-button type="primary" class="addSurvey" icon="el-icon-error" @click="deleteSurvey()" >删除选中问卷</el-button>
        </el-col>
    </el-row> 
    <el-row >
        <el-col>
            <el-table :data="tableData.message.list" ref="tableData.message.list" @selection-change="handleSelectionChange" stripe >
               <el-table-column  type="selection" width="55"></el-table-column>
               <el-table-column label="标题" prop='title' width="200"></el-table-column>
               <el-table-column label="类型" prop='templateType'  width="100"></el-table-column>
               <el-table-column label="适用群体" prop='categoryIds'  width="100"></el-table-column>
               <el-table-column label="收集状态" :filter-method="filterHandler" :filters="FormSort.StatusSort" prop='status'  width="100"><template slot-scope="scope"><i class="el-icon-success" v-if=" scope.row.status == '已发布'"></i><i class="el-icon-info" v-if="scope.row.status == '已保存'"></i><span style="margin-left: 10px">{{ scope.row.status }}</span></template></el-table-column>
               <el-table-column label="收到数据" prop='Received_data'  width="100"></el-table-column>
               <el-table-column label="创建时间" :filter-method="filterHandler" :filters="FormSort.DataSort" sortable width="150" prop='createTime'><template slot-scope="scope"><i class="el-icon-time"></i><span style="margin-left: 10px">{{ scope.row.createTime }}</span></template></el-table-column>
               <el-table-column label="编辑时间" sortable width="150" prop="updateTime"><template slot-scope="scope"><i class="el-icon-time"></i><span style="margin-left: 10px">{{ scope.row.updateTime }}</span></template></el-table-column>
               <el-table-column label="操作" width=""><template slot-scope="scope"><el-button size="mini" @click="handleEdit(scope.$index, scope.row);routerLink(pathItems[0].path,1)">编辑</el-button><el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button><el-button size="mini" type="info">数据</el-button></template></el-table-column>                                   
            </el-table>
        </el-col>
    </el-row>
    <el-row class="pagenation">
        <el-col :span="6" :offset="6">
            <el-pagination :total="tableData.message.total" :current-page.sync="currentPage" :size="tableData.message.pageSize" background :pager-count="7" :page-size="10" :page-count="tableData.message.pages" layout="total, prev, pager, next,jumper"  @current-change="handleCurrentChange"></el-pagination>
        </el-col>
    </el-row>
</div>
</template>


<script>
import * as Survy_Api from '@/Questionnaire_interface/api.js'
import Qs from 'qs'
//Vue.prototype.$Survy_Api = Survy_Api  //将方法写入vue原型
export default {
    data(){
        return{
          currentPage:1,
          PreBatchDeletion:[],
          items:[],     //用来存放编辑室获取的数据
          BatchDeletion:[],
          FormSort:{
            StatusSort:[{
              text:"已发布",
              value:"已发布"
            },
            {
              text:"已保存",
              value:"已保存",
            }],
            DataSort:[],
          },
		      isLoading:false,//是否加载过度动画
          tableData:{message:{
            pageSize:5,
            list:[{
              title:'会议一',
              templateType:'类型x',
              condition:'未发布',             
              Received_data:'0',
              createTime:'2018-1-20',
              updateTime:'2018-1-20',              
          },
          {
              title:'会议二',
              templateType:'类型x',
              condition:'未发布',             
              Received_data:'0',
              createTime:'2018-1-20',
              updateTime:'2018-1-20',            
              
          }]}},
          pathItems:[     //要跳转的路径数组
            {
              path:"/exhibition/questionnare/questionnareEditing"
            },
          ]  
        }
    },
    methods:{
        addSurvey(){
            let that =this;
            console.log(Survy_Api.default.addSurvey);
            let data = {"title":"问卷题目","remark":"这里写备注","templateType":0,"status":0,"categoryIds":[1],"questions":[]};  //要转成json字符串再发
            /*  var   gettype=Object.prototype.toString;
            console.log(gettype.call(data)); */
            //data = JSON.stringify(data);
            /* console.log(data);
            console.log(gettype.call(data)); */
            localStorage.setItem("XSurveyChange","之前未编辑");
            Survy_Api.default.addSurvey(that,data);   //这里问卷还是草稿阶段，没有发表！同时将它传进localstorage
            localStorage.setItem("XSurveyInformation",JSON.stringify(data));
            /*  const loading = this.$loading({ 
                lock: true, 
                text: '创建问卷中', 
                spinner: 'el-icon-loading', 
                background: 'rgba(0, 0, 0, 0.7)', 
                target: document.querySelector('.div1') 
              }); 
              setTimeout(() => {
                loading.close();
              }, 2000); */
              
        },
        routerLink(path,id){
          var time = false;
            setTimeout(() => {
               this.$router.push({
                path:path,
               /*  query:{
                   id:id,
                } */
                })
              }, 2000);  
              },
              init(){
                let data = JSON.parse(window.localStorage.getItem("CurrentSurveyListPage"));
                //data = {"userId":0};   //查看问卷列表需要的用户id
                this.currentPage = JSON.parse(window.localStorage.getItem("CurrentSurveyListPage")).pageNum;
                data = Qs.stringify(data); 
                let that =  this;
                Survy_Api.default.search(that,data);
              },
              handleCurrentChange(val){  //封装一次全部按键可用，不用设置别的上一页下一页之类的
                let that =this;
                let val_tostring = val.toString();
                let data = {"userId":0,"pageNum":val};
                window.localStorage.setItem("CurrentSurveyListPage",JSON.stringify(data));
                data = Qs.stringify(data);   //以后看清楚接口形式！！！
                //window.localStorage.setItem("CurrentSurveyListPage",data);   //每一次跳转保存当前页面页数！
                Survy_Api.default.search(that,data);
                this.currentPage = JSON.parse(window.localStorage.getItem("CurrentSurveyListPage")).pageNum;
              },
              handleDelete(index, row){
                let that =this;
                //let val_tostring = row.id.toString();
                let data = {"ids":""};
                let deleteArray = [row.id];
                data.ids = deleteArray;
                console.log(data);
                data = Qs.stringify(data);
                console.log(row.id);
                this.$confirm('此操作将永久删除此问卷，此操作不可撤销，是否继续？','温馨提示',{
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type:'warning'
                }).then(()=>{
                  Survy_Api.default.deleteSurvey(that,data);
                  Survy_Api.default.search(that,window.localStorage.getItem("CurrentSurveyListPage"));
                  this.currentPage = window.localStorage.getItem("CurrentSurveyListPage").pageNum;
                }).catch(()=>{
                  that.$message({
                    type:'info',
                    message:'已取消删除!'
                  })
                })
              },
              handleEdit(index,row){
                let that = this;
                let data ={"id" : row.id,};  //问卷id鸭
                const loading = this.$loading({ 
                  lock: true, 
                  text: '创建问卷中', 
                  spinner: 'el-icon-loading', 
                  background: 'rgba(0, 0, 0, 0.7)', 
                  target: document.querySelector('.div1') 
                }); 
                Survy_Api.default.getSurveyInfo(that,data);
                localStorage.setItem("XSurveyChange","之前已编辑");
                setTimeout(() => {
                  loading.close();
                }, 2000); 
                
              },
              filterHandler(value, row, column) {     //表格筛选函数！
                const property = column['property'];
                return row[property] === value;
              },
              handleSelectionChange(val){  //form的选中数组
                this.PreBatchDeletion = val; 
              },
              deleteSurvey(){   //批量删除问卷函数
              let that = this;
                //console.log(this.BatchDeletion);
                this.$confirm('此操作将永久删除选中的问卷，此操作不可撤销，是否继续？','温馨提示',{
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type:'warning'
                }).then(()=>{
                  if(this.PreBatchDeletion.length == 0){
                     that.$message({
                        type:'error',
                        message:'未选择一个或多个问卷!'
                      })
                  }else{
                    for(let i =0;i < this.PreBatchDeletion.length;i++){
                      this.BatchDeletion.push(this.PreBatchDeletion[i].id);
                    }
                    let data = {"ids":this.BatchDeletion};
                    console.log(data);
                    data = Qs.stringify(data);
                  Survy_Api.default.deleteSurvey(that,data);
                  this.BatchDeletion = [];  //将要删除的id列表置空
                  this.PreBatchDeletion = []; //将要用来参照删除的id列表置空
                  Survy_Api.default.search(that, window.localStorage.getItem("CurrentSurveyListPage"));
                  }
                }).catch(()=>{
                  that.$message({
                    type:'info',
                    message:'已取消删除!'
                  })
                })
               
              }
        },
        activated(){
          this.init();
        },
        watch:{
                
            }
}
</script>


<style scoped lang="scss">
  #QuestionnareList{
    background: rgba(255, 255, 255, 0.95);
  }
  .el-dropdown {
    vertical-align: top;
  }
.el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-row{
      
  }
  .center_row{  //中间那一个列表的高度
      margin:10px 0 0 0!important; 
  }
  .pagenation{
      background: white;
      padding:5px;
      
  }
  .addSurvey{
      margin:10px 8px;
  } 
</style>


