<template>
  <div id="Questionanswer_wrap">
    <div id="Questionanswer">
        <el-row>
          <div class="title_content wjtitle "><h1>{{items.title}}</h1></div>
        </el-row>
        <el-row>
          <div class="title_content ">{{items.remark}}</div>
        </el-row>
        <el-row>
          <div class="wjhr mtop"></div>
        </el-row>
        <div v-for="(item,key) in items.questions" :key="key">
            <div slot="header" class="clearfix" ><!--每道题题目标题!-->
              <span><p>{{item.serialNumber}}. {{item.title}}</p></span>
            </div>
            <el-form label-position="labelPosition">
                <el-radio-group v-if="item.answerType == 0" v-model="AnswerList[key].choiceId"><!--此处是单选-->
                    <el-radio :label="li.id" v-for="(li,key0) in item.choices" :key="li.serialNumber">{{li.title}}</el-radio>
                    <!-- <el-radio :label="6">备选项2</el-radio>
                    <el-radio :label="9">备选项3</el-radio> -->
                </el-radio-group>
                <el-checkbox-group 
                    v-model="AnswerChoice"
                    :min="2"
                    :max="item.choices.length" v-if="item.answerType == 1">           <!--此处是多选-->
                    <el-checkbox v-for="(li,key1) in item.choices" :key="li.serialNumber" :label="li.id">{{li.title}}</el-checkbox>
                </el-checkbox-group>
                <el-rate
                  v-model="AnswerList[key].content"
                  :colors="['#99A9BF', '#F7BA2A', '#FF9900']" v-if="item.answerType == 3"><!--此处为打分题-->
                </el-rate>
                <el-form-item  v-if="item.answerType == 2&&item.choices == []"  :key="li.serialNumber"><!--此处为单项填空-->
                  <span>{{li.title}}</span>
                  <el-input v-model="AnswerList[key].content"></el-input>
                </el-form-item>
                <el-form-item v-for="(li,key2) in item.choices"  v-if="item.answerType == 2"  :key="li.serialNumber"><!--此处为多项填空-->
                  <span>{{li.title}}</span>
                  <el-input v-model="AnswerSpace[key2+2*key].content"></el-input>   <!--这里的下标有特殊秘密-->
                </el-form-item>
            </el-form>
        </div>
        <el-button type="primary" @click="onSubmit">提交</el-button>
    </div>
  </div>
</template>


<script>
import * as Survy_Api from '@/Questionnaire_interface/api.js'
export default {
    data(){
        return{
            labelPosition:top,   //问题的题目放的方位
            AnswerList:[],    //用来存放回答
            AnswerChoice:[],  //放置多选中的选项id
            AnswerSpace:[],   //用来存放多项填空中的内容
            /* rules: {          //校检表单的规则
                    space: [
                            { required: true, message: '请输入内容', trigger: 'blur' },
                          ],
                    choice: [
                      { required: true, message: '请选择', trigger: 'blur' },
                    ],

            }, */
            items:{
              
                  "id": 2,
                  "title": "问卷标题",
                  "userId": 1,
                  "remark": "感谢你能抽出几分钟来参加本次答题，现在我们马上开始吧！",
                  "templateType": 1,
                  "createTime": "2018-11-16 20:07:29.0",
                  "questions": [],
                 //用来存放请求完的数据，等一下便于放置数据
            
            }
          }
        },
    methods:{
        init(){
          let data ={"id" : 35,};  //问卷id鸭
          let that = this;
          Survy_Api.default.getSurveyInfo(that,data);
          const loading = this.$loading({ 
                  lock: true, 
                  text: '问卷生成中', 
                  spinner: 'el-icon-loading', 
                  background: 'rgba(0, 0, 0, 0.7)', 
                  target: document.querySelector('.div1') 
                }); 
                setTimeout(() => {
                  loading.close();
                  
                }, 2000); 
        },
        onSubmit(){
          let that = this;
           this.$confirm('提交后不可修改答案, 是否继续?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                          const loading = this.$loading({
                                          lock: true,
                                          text: '检查答案中',
                                          spinner: 'el-icon-loading',
                                          background: 'rgba(0, 0, 0, 0.7)'
                                        });
                                        for(let j =0;j<this.items.questions.length;j++){
                                          if(this.items.questions[j].answerType == 2&&this.items.questions[j].choices == 0){  //如果是单项填空
                                              if(this.AnswerList[j].content.replace(/\s|\xA0/g,"") == ""){
                                                  this.$message({
                                                    type: 'error',
                                                    message: '题目还没填写完毕!'
                                                  });
                                                  loading.close();
                                                  return;
                                              }
                                          };
                                          if(this.items.questions[j].answerType == 3){  //如果是打分题
                                              if(this.AnswerList[j].content == 0||this.AnswerList[j].content == ""){
                                                  this.$message({
                                                    type: 'error',
                                                    message: '题目还没填写完毕!'
                                                  });
                                                  loading.close();
                                                  return;
                                              }
                                          };
                                        };
                                        console.log(1);
                                        for(let h = 0;h<this.AnswerSpace.length;h++){
                                          if(this.AnswerSpace[h].content.replace(/\s|\xA0/g,"") == ""){
                                              this.$message({
                                                    type: 'error',
                                                    message: '题目还没填写完毕!'
                                                  });
                                                  loading.close();
                                                  return;
                                            }
                                        };
                                        for(let i = 0;i<this.AnswerList.length&&this.AnswerList.length>0;i++){    //去掉大数组里面的多项填空和多项选择题有bug!
                                          if(this.AnswerList[i].choiceId == "signal"){
                                            this.AnswerList.splice(i--, 1);    //i+1是危险动作！这里涉及一个循环删除数组的操作，贼恶心！
                                            //delete this.AnswerList[i];  
                                          }
                                        };
                                        console.log(this.AnswerList);
                                        for(let i = 0;i<this.AnswerChoice.length;i++){    //将多项选择题加进去
                                            this.AnswerList.push({
                                              "choiceId":this.AnswerChoice[i].choiceId,
                                            })  
                                          }
                                          for(let i = 0; i < this.AnswerSpace.length;i++){    //将多项填空题加进去,可能会有bug因为那个不知道choiceid顺序是否会对应页面生成题目content顺序
                                            this.AnswerList.push({
                                              "choiceId":this.AnswerSpace[i].choiceId,
                                              "content":this.AnswerSpace[i].content,
                                            })  
                                          }

                                          loading.close();
                                          const loading2 = this.$loading({
                                            lock: true,
                                            text: '检查答案完成，提交答案中',
                                            spinner: 'el-icon-loading',
                                            background: 'rgba(0, 0, 0, 0.7)'
                                          }); 
                                          let data ;//=  this.AnswerList;   //不要漏了this
                                          console.log(data);
                                          data = {
                                            "surveyId":this.items.id,
                                            "questions":this.AnswerList
                                          }
                                          Survy_Api.default.addAnswer(that,data);
                                          loading2.close();    
                        }).catch(() => {
                            this.$message({
                              type: 'info',
                              message: '已取消答案提交'
                            });
                                   
                        });
                      
          
          }
          
          
    },
    activated(){
      this.init();
    }
}
</script>


<style scoped lang="scss">
    #Questionanswer{
        background-color: #fff;
        padding: 20px;
        width: 814px;
        margin: 0px auto 10px auto;
        min-height: 200px;
        background: #fff;
        padding: 66px 40px;
        word-break: normal;
        z-index: 999;
        position: relative;   
    }
    .title_content{
        color: #666666;
        font-size: 14px;
        line-height: 1.4;
    }
    .wjtitle {
        float: left;
        width: 100%;
        h1 {
            color: #4C4C4C;
            font-size: 18px;
            line-height: 1.2;
        }
    }
    .wjintro{
      float: left;
      width: 100%;
      font-size: 16px;
      margin-top: 20px;
    }
    #Questionanswer_wrap{
      background-color: #f5f5f5 !important;
    }
    .wjhr {
        width: 100%;
        height: 3px;
        background-color: #53a4f4!important;
    }
    .wjhr {
        float: left;
        width: 100%;
        height: 3px;
        background-color: #f29400;
    }
    .mtop {
        margin-top: 20px;
    }
</style>




