<template>
  <div id="banning">
    <div id="tip">
      <div id="text">该接口暂时还未开放哦</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "banning",
  data() {
    return {};
  }
};
</script>


<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#banning {
  @include max;
  // border: 1px solid rgba(204, 204, 204, 0.575);
}
#text{
  font-size: 20px;
  color: rgb(138, 138, 138);
  text-align: center;
  line-height: 100%;
  margin: 100px;
}
</style>




