      <li class="list_title" class="menu_title">
        <slot name="title">
        </slot>
      </li>
      <li class="list_title" class="list_title" v-for=""></li>
    </ul> -->
    <div>
      <div class="list_left">
        <div class="list_top">
          <li
            class="list_title"
            v-for="(item, index) in items"
            v-if="index<=3"
            @click="routerLink(item.id,currentTab)"
          >
            {{item.title}}
          </li>
        </div>
        <div class="list_bottom">
          <li
            class="list_title"
            v-for="(item, index) in items"
            v-if="index > 3 && index < 9"
            @click="routerLink(item.id,currentTab)"
          >
            {{item.title}}
          </li>
        </div>
      </div>
      <div class="list_right">
        <div class="list_top">
          <div
            :key="index"
            v-for="(pic, index) in filteredpics"
            v-if="index<3"
            @click="routerLink(pic.id,currentTab)"
          >
            <img :src='pic.img'>
            <span
              class="product-title"
              :title="pic.title"
            >{{pic.title}}</span>
          </div>
        </div>
        <div class="list_bottom">
          <li
            class="list_title"
            v-for="(item, index) in items"
            v-if="index >9 && index<15"
            :key="index"
            @click="routerLink(item.id,currentTab)"
          >
            {{item.title}}
          </li>
        </div>
      </div>
    </div>
  </div>
</template>

