export default {
  name: "pb-list3",
  props: ["myMessage", "currentTab", "isRequested"],
  watch: {
    isRequested() {
      this.init();
    }
  },
  data() {
    return {
      items: [
        { title: "大数据小哥哥小姐姐正在努力爬取数据呢" },
        { title: "大数据小哥哥小姐姐正在努力爬取数据呢" },
        { title: "大数据小哥哥小姐姐正在努力爬取数据呢" },
        { title: "大数据小哥哥小姐姐正在努力爬取数据呢" },
        { title: "大数据小哥哥小姐姐正在努力爬取数据呢" },
        { title: "大数据小哥哥小姐姐正在努力爬取数据呢" },
