      //   },
      //   {
      //     img: "./static/PICS/timg3.jpg",
      //     title: "这是一段神奇的介绍"
      //   }
      // ]
    };
  },
  computed: {
    filteredpics() {
      let that = this;
      let data = that.items.filter(function(item) {
        return item.img != null && item.img != "";
      });
      if (data == "" || data == null) {
        data = [
          {
            img:
              "http://122.13.4.239/resources/img/industry_information/5aa896a2d4ea5363.jpg",
            title: "",
            id: ""
          },
          {
            img:
              "http://122.13.4.239/resources/img/industry_information/5aa896a2d4ea5363.jpg",
            title: "",
            id: ""
          },
          {
            img:
              "http://122.13.4.239/resources/img/industry_information/5aa896a2d4ea5363.jpg",
            title: "",
            id: ""
          }
        ];
      }
      return data;
    }
  },
  methods: {
    init() {
      if (this.myMessage) {
        // console.log(this.myMessage);
        this.items = this.myMessage;
        // console.log(this.items);
        // let i = 0;
        // this.myMessage.forEach(a => {
        //   if(a.img!=null && i<3){
        //     this.pics[i].img = a.img
        //     i++
        //   }
        // });
      }
    },
    routerLink(messageId, item) {
      if (this.myMessage) {
        console.log(messageId);
        this.$router.push({
          path: "/tech/industry_dynamic_text",
          query: { messageId: messageId, item: item }
        });
      }
    }
  },
  activated() {
    this.init();
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#pb-list3 {
  @include max;
  // border: 1px solid rgba(204, 204, 204, 0.575);
}
// .fa-title {
