.list_left {
  width: 395px;
  float: left;
  height: 270px;
  overflow: hidden;
  box-sizing: border-box;
  padding: 10px 10px 10px 20px;
}
.list_right {
  & {
    width: 395px;
    float: right;
    box-sizing: border-box;
    padding: 10px 10px;
  }
  img {
    width: 100px;
    height: 100px;
    display: inline-block;
  }
  .product-title {
    display: block;
    position: absolute;
    top: auto;
    right: 0;
    bottom: 0;
    left: 0;
    width: auto;
    padding: 5px 5px 15px 5px;
    // padding-right: 100px;
    height: 10px;
    line-height: 20px;
    background: rgba(0, 0, 0, 0.35);
    color: #fff;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.list_top {
  & {
    box-sizing: border-box;
    height: 110px;
    padding: 0 5px 10px 0;
  }
  div {
    cursor:pointer;
    display: inline-block;
    margin-left: 15px;
    width: 100px;
    height: 100px;
    position: relative;
  }
}
.list_bottom {
  box-sizing: border-box;
  height: 140px;
  padding: 10px 5px 0 0;
}
.list_title {
  color: #3f3f3f;
  display: inline-block;
  max-width: 345px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 15px;
  vertical-align: middle;
  cursor: pointer;
  letter-spacing: 1px;
  line-height: 15px;
  padding: 4px 0 3px 10px;
  font-size: 13px;
  clear: both;
}
.list_title:hover {
  color: #127de7;
}
