<template>
  <!-- <div id="job-search">
    <div class="current">
      <span>当前分类></span>
    </div>
    <div class="menus">
      <div class="row" :class="0 === index ? 'row-first' :''" v-for="(item,index) in items" :key="item.title">
        <div class="head">
          <span class="title" :title='item.title'>{{item.title}}</span>
        </div>
        <div class="body">
          <div class="items">
            <div class="item-inner">
              <li class="item" v-for="(tab,index) in tabs" v-if="tab.belong === item.title" :key="tab.name">
                <a :title="tab.name">{{tab.name}}</a>
              </li>
            </div>
          </div>
        </div>
        <div class="foot">

        </div>
      </div>
    </div>
  </div> -->
  <div id="job-search">
    <div class="menus">
      <!-- 地区 -->
      <div class="row row-first">
        <div class="head">
          <span
            class="title"
            title='地区'
          >地区：</span>
        </div>
        <div class="body">
          <!-- 省份 -->
          <el-select
            v-model="value1"
            filterable
            placeholder="选择省份/直辖市/自治区"
            @change="changeArea"
          >
            <el-option
              v-for="(item,index) in options1"
              :key="index"
              :label="item.name"
              :value="item.id"
            >
              <!-- <span style="float: left">{{ item.label }}</span> -->
              <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
          <!-- 城市 -->
          <el-select
            v-model="value2"
            :disabled="isBan[0]"
            placeholder="选择城市"
            multiple
            @change="AreaChoose"
          >
            <el-option-group
              v-for="group in options2"
              :key="group.label"
              :label="group.label"
            >
              <el-option
                v-for="item in group.options"
                :key="item.name"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-option-group>
          </el-select>
        </div>
      </div>
      <!-- 行业职位 -->
      <div class="row">
        <div class="head">
          <span
            class="title"
            title='行业/职位：'
          >行业/职位：</span>
        </div>
        <div class="body">
          <!-- 行业1 -->
          <el-select
            v-model="value3"
            filterable
            placeholder="行业类型"
            @change="changeIndustry1"
          >
            <el-option
              v-for="(item,index) in options3"
              :key="index"
              :label="item.name"
              :value="item.id"
            >
              <!-- <span style="float: left">{{ item.label }}</span> -->
              <span style="color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
          <!-- 行业2 -->
          <el-select
            v-model="value4"
            :disabled="isBan[1]"
            filterable
            placeholder="行业"
            @change="changeIndustry2"
          >
            <el-option
              v-for="(item,index) in options4"
              :key="index"
              :label="item.name"
              :value="item.id"
            >
              <!-- <span style="float: left">{{ item.label }}</span> -->
              <span style="color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
          <!-- 职位 -->
          <el-select
            v-model="value5"
            :disabled="isBan[2]"
            filterable
            placeholder="职位"
            @change="industryChoose"
          >
            <el-option
              v-for="item in options5"
              :key="item.name"
              :label="item.name"
              :value="item.id"
            >
              <!-- <span style="float: left">{{ item.label }}</span> -->
              <span style="color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
        </div>
      </div>
      <el-form
        :inline="true"
        :model="filters"
      >
        <el-form-item>
          <el-input
            v-model="filters.name"
            clearable
            placeholder="默认搜索显示招聘列表"
            style="min-width: 240px;width:400px;"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="throttle(searching,this)"
          >搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Qs from "qs";

export default {
  naem: "job-search",
  data() {
    return {
      filters: {
        name: ""
      },
      options1: [
        {
          id: "",
          name: "所有地区",
          label: "所有地区"
        }
        // {
        //   id: "",
        //   name: "GuangDong",
        //   label: "广东"
        // },
        // {
        //   id: "",
        //   name: "BeiJing",
        //   label: "北京"
        // },
        // {
        //   id: "",
        //   name: "ShangHai",
        //   label: "上海"
        // },
        // {
        //   id: "",
        //   name: "TaiWan",
        //   label: "台湾"
        // },
        // {
        //   id: "",
        //   name: "HongKong",
        //   label: "香港自治区"
        // }
      ],
      options2: [
        {
          label: "热门城市",
          options: [
            // {
            //   id: 1,
            //   name: "上海",
            //   label: "上海"
            // },
            // {
            //   id: 2,
            //   name: "北京",
            //   label: "北京"
            // },
            // {
            //   id: 3,
            //   name: "广州",
            //   label: "广州"
            // },
            // {
            //   id: 4,
            //   name: "深圳",
            //   label: "深圳"
            // }
          ]
        },
        {
          label: "城市名",
          options: [
            {
              id: 0,
              name: "所有城市",
              label: "所有城市"
            }
          ]
        }
      ],
      options3: [
        {
          id: "",
          name: "所有行业",
          label: "所有行业"
        }
      ],
      options4: [],
      options5: [],
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      isBan: [false, false, false],
      industryId: "",
      regionIds: [],
      url: [
        "/position/getLevelAreas",
        "/position/getLevelIndustry",
        "/position/search"
      ], //[各级地区，各级行业，搜索]
    };
  },
  methods: {
    init() {
      let that = this;
      let url = that.HOST + that.url[0];
      that.axios
        .get(url)
        .then(response => {
          that.options1 = [
            {
              id: "",
              name: "所有地区",
              label: "所有地区"
            }
          ];
          that.options1.push.apply(that.options1, response.data.message); //合并数组
          // console.log(that.options1);
        })
        .catch(function(response) {
          that.$message.error({
            showClose: true,
            message: "搜索地区数据异常",
            duration: 2000
          });
        });
      url = that.HOST + that.url[1];
      that.axios
        .get(url)
        .then(response => {
          that.options3 = [
            {
              id: "",
              name: "所有行业",
              label: "所有行业"
            }
          ];
          that.options3.push.apply(that.options3, response.data.message); //合并数组
          // console.log(that.options3);
        })
        .catch(function(response) {
          that.$message.error({
            showClose: true,
            message: "搜索行业数据异常",
            duration: 2000
          });
        });
    },
    //二级地区
    changeArea(sel) {
      let that = this;
      // const array = that.options1.filter(a => {
      //   return a.name == sel;
      // });
      // console.log(array);
      if (sel != "") {
        let url = that.HOST + that.url[0];
        let data = { parentId: sel };
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            if (response.data.message != "") {
              that.options2[1].options = [];
              //合并数组
              that.options2[1].options.push.apply(
                that.options2[1].options,
                response.data.message
              );
              that.isBan[0] = false;
            } else {
              that.options2 = [];
              that.isBan[0] = true;
              that.value2 = "";
            }
          })
          .catch(response => {
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      }
    },
    //二级行业
    changeIndustry1(sel) {
      let that = this;
      // const array = that.options3.filter(a => {
      //   return a.name == sel;
      // });
      // console.log(array);
      if (sel != "") {
        let url = that.HOST + that.url[1];
        let data = { parentId: sel };
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            if (response.data.message != "") {
              that.options4 = response.data.message;
              //合并数组
              that.isBan[1] = false;
            } else {
              that.options4 = [];
              that.isBan[1] = true;
              that.isBan[2] = true;
              that.value4 = that.value5 = "";
            }
          })
          .catch(response => {
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      }
    },
    //三级行业
    changeIndustry2(sel) {
      let that = this;
      // const array = that.options4.filter(a => {
      //   return a.name == sel;
      // });
      // console.log(array);
      if (sel != "") {
        let url = that.HOST + that.url[1];
        let data = { parentId: sel };
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            if (response.data.message != "") {
              that.options5 = response.data.message; //合并数组
              that.isBan[2] = false;
            } else {
              that.options5 = [];
              that.isBan[2] = true;
            }
          })
          .catch(response => {
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      }
    },
    //地区选中
    AreaChoose(sels) {
      this.regionIds = sels;
    },
    //行业选中
    industryChoose(sel) {
      this.industryId = sel;
    },
    //搜索
    searching() {
      let that = this;
      let url = that.HOST + "/position/search";
      let params = Qs.stringify({
        content: that.filters.name.trim(),
        regionIds: that.regionIds,
        industryId: that.industryId
      });
      that
        .axios({
          method: "post",
          data: params,
          url: url
        })
        .then(response => {
          let items = JSON.stringify(response.data.message);
          let loading = true;
          that.$router.push({
            path: "/talent/talent-search",
            query: { loading: loading, item: items }
          });
          that.$emit("jobSearch", loading, response.data.message);
        })
        .catch(response => {
          that.$message.error({
            showClose: true,
            message: "获取招聘信息出现异常",
            duration: 2000
          });
        });
    },
  },
  activated() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
.active {
  color: #409eff !important;
}
#job-search {
  @include max;
  overflow: hidden;
  padding-bottom: 10px;
}
.current {
  padding: 5px 15px;
  color: #666;
  max-width: 80%;
}
.menus {
  & {
    border: 1px solid #e8e8e8;
    margin: 2px 5px;
    padding: 5px;
  }
  .row {
    & {
      position: relative;
      border-top: 1px dashed #dedede;
      margin: 0 5px;
      background: #f4f4f4;
    }
    .head {
      & {
        position: absolute;
        left: 11px;
        top: 15px;
        color: rgb(78, 78, 78);
      }
      .title {
        float: left;
        max-width: 100px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-weight: 400;
      }
    }
    .body {
      & {
        padding: 6px 80px 7px 85px;
      }
      // .items {
      //   & {
      //     height: 36px;
      //     overflow: hidden;
      //   }
      //   .item {
      //     & {
      //       float: left;
      //       margin: 9px 25px 9px 0;
      //       height: 18px;
      //       color: #000;
      //       text-decoration: none;
      //     }
      //     a {
      //       float: left;
      //       max-width: 140px;
      //       white-space: nowrap;
      //       overflow: hidden;
      //       text-overflow: ellipsis;
      //       cursor: pointer;
      //     }
      //     a:hover {
      //       @extend .active;
      //     }
      //   }
      // }
    }
  }
  .row-first {
    border-top: none;
  }
  .el-select {
    margin: 3px 15px 3px 20px;
  }
  .el-form {
    padding: 5px 100px;
  }
  .el-form-item {
    margin: 5px;
  }
}
</style>



