<!--index.vue-->
<template>
  <div id="app">
    <div class="bg-img">
      <div class="bg-content">
        <m-header
          ref="c1"
          @controlSign="controlSign1"
          :username="username"
          :Rank="Rank"
          @userSignOut="userSignOut"
        ></m-header>
        <keep-alive>
          <router-view
            :username="username"
            :Rank="Rank"
            @userSignIn="userSignIn"
            @userSignOut="userSignOut"
            @loadFinish="loadFinish"
          ></router-view>>
        </keep-alive>
        <sign
          id="sign"
          ref="sign"
          v-show="showSign"
          @controlSign="controlSign1"
          @userSignIn="userSignIn"
        ></sign>
        <div
          v-if="showSign"
          class="background-wall"
        ></div>
        <m-footer></m-footer>
        <div
          class="g-load"
          v-show="loading"
        >
          <div class="loader">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";
import MFooter from "@/components/common/m-footer";
import Sign from "@/components/sign/sign";

// var WIDTH = document.documentElement.clientWidth, HEIGHT = document.documentElement.clientHeight, POINT = 400;

export default {
  name: "robot",
  components: {
    MHeader,
    MFooter,
    Sign
  },
  data() {
    return {
      Rank: 0, //权限等级
      path: "",
      showSign: false,
      username: sessionStorage.username,
      loading: false
    };
  },

  methods: {
    controlSign1(result, type) {
      let that = this;
      //判断跳出页面
      if (type) {
        this.$refs.sign.selectPage(type);
        setTimeout(function() {
          //显示跳出页面
          that.showSign = result;
        }, 200);
      } else {
        this.showSign = result;
      }
    },
    userSignIn(username) {
      this.permissionJudge();
      sessionStorage.username = username;
      this.username = sessionStorage.username;
    },
    userSignOut() {
      this.permissionJudge();
      sessionStorage.username = "";
      this.username = sessionStorage.username;
    },
    loadFinish(a) {
      this.loading = a;
    },
    //判断权限等级
    permissionJudge() {
      let that = this;
      that.axios
        .get(that.HOST + "/user/getPermission")
        .then(response => {
          if (response.data.result == "success") {
            that.Rank = response.data.message;
            localStorage.setItem("userRank", response.data.message);
          }
        })
        .catch(res => {
          that.$message.error({
            showClose: true,
            message: "获取登录状态失败",
            duration: 2000
          });
        });
    }
  },
  //监听路由变化获取当前路由
  watch: {
    $route() {
      // 获取当前路径
      let path = this.$route.path;
      // 检索当前路径
      this.$refs.c1.checkRouterLocal(path);
      this.permissionJudge(); //判断权限等级
    }
  },
  mounted: function() {
    // console.log(this.username);
    // 获取当前路径
    let path = this.$route.path;
    // console.log(path);
    // 检索当前路径
    this.$refs.c1.checkRouterLocal(path);
  }, //确保刷新后页面初始化不出错
  created() {
    this.permissionJudge(); //判断权限等级
  }
  // created: function() {
  //   if (localStorage.getItem("userRank")) {
  //     let i = localStorage.getItem("userRank");
  //     if (i === 0 || i) {
  //       this.defined.setRank(i);
  //     }
  //   }
  // console.log(this.isAdmin, this.defined.isAdmin);
  // }
};
</script>

<style lang="scss">
body {
  margin: 0;
  font-size: 16px;
  font-family: "微软雅黑", "Microsoft Yahei", Verdana, sans-serif;
  min-width: 1002px;
  // background: url(/static/PICS/ui-background.jpg) center top repeat rgb(255, 255, 255);
  // background: url(http://image.woshipm.com/wp-files/2014/09/72f9850c5a1e72f4ea873584fbb3959c.jpg) center top repeat rgb(255, 255, 255);
}
.bg-img {
  position: relative;
  // width: 100%;
  min-width: 1200px;
  height: 100%;
  background: url(http://image.woshipm.com/wp-files/2014/09/72f9850c5a1e72f4ea873584fbb3959c.jpg)
    center top repeat;
}
.bg-img::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to bottom right, #26486e, #cc998f);
  opacity: 0.3;
  z-index: 1;
}
.bg-content {
  z-index: 2;
  position: relative;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
input:focus {
  outline: none;
}
a {
  text-decoration: none;
  color: inherit;
}
.background-wall {
  background-color: #000000;
  width: 100%;
  height: 100%;
  top: 0;
  position: fixed;
  clear: both;
  z-index: 5;
  opacity: 0.5;
}

// loading
$speed: 2.5s;

.g-load {
  position: fixed;
  z-index: 2000;
  background: radial-gradient(
    rgba(238, 238, 238, 0.932),
    rgba(68, 68, 68, 0.932)
  );
  margin: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.loader {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: auto;
  width: 175px;
  height: 100px;
  span {
    display: block;
    background: #ccc;
    width: 7px;
    height: 10%;
    border-radius: 14px;
    margin-right: 5px;
    float: left;
    margin-top: 25%;
    &:last-child {
      margin-right: 0px;
    }
    &:nth-child(1) {
      animation: load $speed 1.4s infinite linear;
    }
    &:nth-child(2) {
      animation: load $speed 1.2s infinite linear;
    }
    &:nth-child(3) {
      animation: load $speed 1s infinite linear;
    }
    &:nth-child(4) {
      animation: load $speed 0.8s infinite linear;
    }
    &:nth-child(5) {
      animation: load $speed 0.6s infinite linear;
    }
    &:nth-child(6) {
      animation: load $speed 0.4s infinite linear;
    }
    &:nth-child(7) {
      animation: load $speed 0.2s infinite linear;
    }
    &:nth-child(8) {
      animation: load $speed 0s infinite linear;
    }
    &:nth-child(9) {
      animation: load $speed 0.2s infinite linear;
    }
    &:nth-child(10) {
      animation: load $speed 0.4s infinite linear;
    }
    &:nth-child(11) {
      animation: load $speed 0.6s infinite linear;
    }
    &:nth-child(12) {
      animation: load $speed 0.8s infinite linear;
    }
    &:nth-child(13) {
      animation: load $speed 1s infinite linear;
    }
    &:nth-child(14) {
      animation: load $speed 1.2s infinite linear;
    }
    &:nth-child(15) {
      animation: load $speed 1.4s infinite linear;
    }
  }
}

@keyframes load {
  0% {
    background: #ccc;
    margin-top: 25%;
    height: 10%;
  }
  50% {
    background: #444;
    height: 100%;
    margin-top: 0%;
  }
  100% {
    background: #ccc;
    height: 10%;
    margin-top: 25%;
  }
}
</style>


