<template>
    <div id="Singlefill">
        <el-card shadow="hover">
            <div slot="header" class="clearfix">
              <span>题目{{bean+1}}</span>
            </div>
            <el-row>
                <el-col class="SinglefillInput" :span="22">
                    <el-input placeholder="填空题"   v-model="QuestionList.questions[bean].choices[0].title">
                    </el-input>
                </el-col>
                <el-col :span="1" :offset="1">
                   <el-button type="text" class='fa fa-trash-o fa-2x' @click="delete_all"></el-button>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-input :disabled="true">
                    </el-input>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>
<script>
export default {
    data(){
        return{
              question:{
                        "serialNumber":1,     //题目序号                              没有发送0过来
                        "title":"单项填空题标题", //题目标题
                        "answerType":2,       //单选：0，多选：1，文字题：2，打分题：3
                        "optionType":0,       //必选：0，可选：1
                        "minNumber":1,        //最少选项数（多选）、最少字数（文字题）    没有发送0过来
                        "maxNumber":1,        //最多选项数（多选）、最少字数（文字题）    没有发送0过来
                        "page":0,              //页数，不分页发0
                        //"precedentChoiceId":1,//关联选项id,                            **没有不要发送过来**
                        //"precedentQuestionId":, //关联题目id                          **没有不要发送过来**
                        "choices":            //选项
                        [
                            
                        ]
            }
        }
    },
    props:{
        bean:{

        },
        isSend:{

        },
        QuestionList:{

        },
    },
    methods:{
          delete_all(){   //删除整道题目
            this.$emit('delete_big',this.bean);
            }
    },
    watch:{
        isSend:function(){
            /* this.question.serialNumber = this.bean+1;
            this.question.answerType = 2;
            this.QuestionList.questions = this.question; */
        },
        QuestionList:function(){
            this.QuestionList.questions[this.bean].serialNumber = this.bean + 1;
            this.QuestionList.questions[this.bean].answerType = 2; 
            console.log(this.QuestionList);
        }
    }
 
}
</script>
<style scoped lang='scss'>
  .SinglefillInput{
    height:50px;
  }
 .el-card{
    padding:25px;
  }
.el-input{
    margin:10px 0;
  }
</style>


