<template>
<div id="QuestionnareEditing">
   <el-row >
        <el-col :span="colwidth" :class="searchBarFixed == true ? 'isFixed' :''" id="MenuList">
        <div class="Istoggle" @click="function(){isCollapse = !isCollapse;if(colwidth==4){colwidth = 1;ColOffset=1;}else{colwidth = 4;ColOffset=4;}}" >
            <i class="el-icon-arrow-down" v-show="!isCollapse"></i>
            <i class="el-icon-more" v-show="isCollapse"></i>
        </div>
        <el-menu :collapse="isCollapse" >
            <el-submenu index='1'>
                <template slot='title'>
                    <i class="el-icon-success"></i>
                    <span>选择题</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item  index="1-1" @click="addQuestion('1-1','')">
                            单选题
                        </el-menu-item>
                        <el-menu-item  index="1-2" @click="addQuestion('1-2','')">
                            多选题
                        </el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
            <el-submenu index='2' >
                <template slot='title'>
                    <i class="el-icon-edit"></i>
                    <span>填空题</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item  index="2-1" @click="addQuestion('2-1','')">
                            单项填空题
                        </el-menu-item>
                        <el-menu-item  index="2-2" @click="addQuestion('2-2','')">
                            多项填空题
                        </el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
            <el-submenu index='2' >
                <template slot='title'>
                    <i class="el-icon-star-off"></i>
                    <span>打分题</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item  index="3-1" @click="addQuestion('3-1','')">
                            单项打分题
                        </el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
        </el-menu>
        </el-col >
        <el-col :span="20"  :offset="ColOffset">
            <el-container>
                <el-header><!--这里放问卷的标题-->
                    <el-card>
                      <el-input placeholder="问卷标题" clearable autosize v-model="QuestionList.title"></el-input>
                      <el-input placeholder="感谢你能抽出几分钟来参加本次答题，现在我们马上开始吧！" clearable autosize v-model="QuestionList.remark"></el-input>
                    </el-card>
                </el-header>
                <el-main><!--这里放动态添加的题目-->              
                  <el-card  v-for="(item,key) in QuestionList.questions" :key="key" track-by="$index">
                    <component :isSend="isSend"  :is="items[key].component" :bean="key" :QuestionList.sync="QuestionList"  @listenComponentData="listenComponentData" @delete_big="delete_main" ></component>
                  </el-card>
                </el-main>
                <el-footer><!--这里放感谢标志的题目-->
                    <el-card>
                        <el-row>
                            <el-col>
                              <el-input placeholder="您已完成本次问卷，感谢您的帮助和支持！" clearable autosize :disabled="true"></el-input>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :offset="9" :span="3">
                              <el-button class="sharebutton" type="primary" @click="comfirm('发布');routerLink('/exhibition/questionnare',1)">发布并分享</el-button>
                            </el-col>
                            <el-col  :span="3" :offset="1">
                              <el-button class="sharebutton" type="primary" @click="comfirm('保存');routerLink('/exhibition/questionnare',1)">保存到草稿</el-button>
                            </el-col>
                        </el-row>
                    </el-card>
                </el-footer>
            </el-container>
        </el-col>
   </el-row>
</div>
</template>


<script>
  import Singlefill from '@/components/Questionnaire/Single-fill-in-the-blank'
  import Multifill from '@/components/Questionnaire/Multiple-fill'
  import Multichoice from '@/components/Questionnaire/Multiple-choice'
  import Singlechoice from '@/components/Questionnaire/Single-choice-question'
  import Scoreproblem from '@/components/Questionnaire/Score-problem'
  import Qs from 'qs'
  import * as Survy_Api from '@/Questionnaire_interface/api.js'
  
export default {
  components:{
    Singlefill,
    Multifill,
    Multichoice,
    Singlechoice,
    Scoreproblem,
  },
    data(){
        return{
            ColOffset:0.5,//左边导航条一旦固定时右边的offset值！
            searchBarFixed:false,     //左边导航条是否固定！
            isSend:false,    //是否发布问卷用来提示下面的子组件是否回收信息
            colwidth:4,
            surveyId:1,//保存问卷id
            isCollapse:false,  //是否折叠的标志
            items:[],     //现在的已添加的组件列表的组件名称，对象数组
            m:1,
            //下面式定义基本的
            QuestionList:{title:"问卷题目",  //标题
            remark:"这里写备注", //备注
            templateType:0,    //问卷：0，模板：1
            status:0,          //状态，发布：0，草稿：1
            categoryIds:[1] ,   //群体分类，可先用1-12测试
            questions:[],
            },
            cpn:[{        //列表对应的组件顺序，静态的
                index:'1-1',
                comp:'Singlechoice',    //组件名字
            },
            {
                index:'1-2',
                comp:'Multichoice'
            },
            {
                index:'2-1',
                comp:'Singlefill'
            },
            {
                index:'2-2',
                comp:'Multifill'
            },
            {
                index:'3-1',
                comp:'Scoreproblem'
            }]
        }
    },
    methods:{
      addQuestion(index,text){  //给组件的数组添加单项组件
      let component;
      let serialNumber;  //题目序号
      for(let i =0;i<5;i++){
          if(this.cpn[i].index == index){
              component = this.cpn[i].comp;
          }
      }
        this.items.push({
          'component':component,  //组件名字
          'text':this.m++,            //组件需要的信息
        });
        switch(component){    //给QuestionList.questions赋值
          case 'Singlechoice':    //如果是单选的话
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"题目标题",
              "answerType":0,
              "optionType":0,
              "minNumber":1,
              "maxNumber":1,
              "page":0,              //页数，不分页发0
              "choices":[
                {
                "serialNumber":1,         //序号                        没有发送0过来
                "title":"选项标题",   //标题
                "type":0,                  //选项型:0、文字型:1
                "defaultStatus":0         //默认状态                      没选就发0
                },
                {
                "serialNumber":2,
                "title":"选项标题",
                "type":0,
                "defaultStatus":0         //默认状态                      没选就发0
                },
                  ],            //选项})
                });
                break;
          case 'Multichoice':   //如果是多选的话
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"题目标题",
              "answerType":1,
              "optionType":0,
              "minNumber":2,
              "maxNumber":0,
              "page":0,              //页数，不分页发0
              "choices":[
                {
                "serialNumber":1,         //序号                        没有发送0过来
                "title":"选项标题",   //标题
                "type":0,                  //选项型:0、文字型:1
                "defaultStatus":0         //默认状态                      没选就发0
                },
                {
                "serialNumber":2,
                "title":"选项标题",
                "type":0,
                "defaultStatus":0         //默认状态                      没选就发0
                },
                  ],            //选项})
                });
                break;
          case 'Singlefill':   //如果是单项填空的话
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"题目标题",
              "answerType":2,
              "optionType":0,
              "minNumber":1,
              "maxNumber":1,
              "page":0,              //页数，不分页发0
              "choices":[
                {
                "serialNumber":1,         //序号                        没有发送0过来
                "title":"选项标题",   //标题
                "type":1,                  //选项型:0、文字型:1
                "defaultStatus":0         //默认状态                      没选就发0
                },
                  ],            //选项})
                });
                break;
          case 'Multifill':   //如果是多项填空的话
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"题目标题",
              "answerType":2,
              "optionType":0,
              "minNumber":2,
              "maxNumber":0,
              "page":0,              //页数，不分页发0
              "choices":[
                {
                "serialNumber":1,         //序号                        没有发送0过来
                "title":"选项标题",   //标题
                "type":1,                  //选项型:0、文字型:1
                "defaultStatus":0         //默认状态                      没选就发0
                },
                {
                "serialNumber":2,
                "title":"选项标题",
                "type":1,
                "defaultStatus":0         //默认状态                      没选就发0
                },
                  ],            //选项})
                });
                break;
          case 'Scoreproblem':   //如果是打分题的话，那肯定没有那个选项呀
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"题目标题",
              "answerType":3,
              "optionType":0,
              "minNumber":1,
              "maxNumber":1,
              "page":0,              //页数，不分页发0
              "choices":[],            //选项})
                });
                break;      
        }
      },
      delete_main(index){     //被子组件唤醒删除这整道题目
          //this.QuestionList.questions = [];  //将整个数组的问题列表置空方便下面重新赋值来保存！
          this.QuestionList.questions.splice(index,1);  //删除哪一项
          this.items.splice(index,1);   //删除哪一项 
      },
      routerLink(path,id){
        var time = false;
        setTimeout(() => {
        this.$router.push({
         path:path,    
        })
        }, 2000);  
        },
      comfirm(signal){
        if(signal == "保存"){
          this.QuestionList.status = 1;
        }else if(signal == "发布"){
          this.QuestionList.status = 0;
        }
        var that = this;
          this.$confirm('此操作将发布问卷，是否继续','温馨提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
          }).then(() => {
                that.isSend = !that.isSend;   //通知下面的组建要回收那个信息！
                console.log(this.QuestionList);
                //var data =JSON.stringify(this.QuestionList);
                if(localStorage.getItem("XSurveyChange") == "之前已编辑"){
                  Survy_Api.default.updateSurvey(that,this.QuestionList);
                }else if(localStorage.getItem("XSurveyChange") == "之前未编辑"){
                  Survy_Api.default.addSurvey(that,this.QuestionList);
                }

                const loading = this.$loading({ 
                  lock: true, 
                  text: '创建问卷中', 
                  spinner: 'el-icon-loading', 
                  background: 'rgba(0, 0, 0, 0.7)', 
                  target: document.querySelector('.div1') 
                }); 
                setTimeout(() => {
                  loading.close();
                  
                }, 2000); 
                
/*                 that.$http.post(vm.testUrl,data, {headers:{'Content-Type':'application/x-www-form-urlencoded'}}).then((response)=>{
                console.log(response.data.msg);
                }).then((response)=>{
                  if(response.data.result == 'success'){
                    this.$message({
                        type: 'success',
                        message: '发布成功！'
                    })
                  }else if(response.data.result == 'error'){
                    this.$message({
                        type: 'error',
                        message: '网页编写错误，请联系网页管理者修改！'
                    })  
                  }
                }).catch((response) =>{
                       this.$message({
                        type: 'error',
                        message: '发布失败！网络抽风了qaq！'
                    }) 
                    }) */

             
          }).catch(() => {
              this.$message({
                  type: 'info',
                  message: '已取消发布！'
              })
          })
      },
      listenComponentData(childArray){
        this.QuestionList.push({  //若干形式

        })
      },
      init(){     //一开始初始化从localstorage的地方拿到那个保存的问卷序列
        this.QuestionList = JSON.parse(localStorage.getItem("XSurveyInformation"));
        let component;
        let serialNumber;  //题目序号
        for(let i =0;i<this.QuestionList.questions.length;i++){
            if(this.QuestionList.questions[i].answerType ==0){
              component = "Singlechoice";
            }else if(this.QuestionList.questions[i].answerType ==1){
              component = "Multichoice";
            }else if(this.QuestionList.questions[i].answerType ==3){
              component = "Scoreproblem";
            }else if(this.QuestionList.questions[i].answerType ==2&&this.QuestionList.questions[i].choices.length == 0){
              component = "Singlefill";
            }
            else if(this.QuestionList.questions[i].answerType ==2&&this.QuestionList.questions[i].choices.length != 0){
              component = "Multifill";
            };
            this.items.push({
            'component':component,  //组件名字
            'text':this.m++,            //组件需要的信息
          });
        } 
        },
        handleScroll () {         //监听滚动条事件
          var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
          console.log(scrollTop);
          var offsetTop = document.getElementById("MenuList").offsetTop
          if (scrollTop > 336) {   //因为被组件化了挡住上面的高度了所以自己测咯？
            this.searchBarFixed = true;
            this.ColOffset = 4;
          } else {
            this.searchBarFixed = false;
            this.ColOffset = 0.5;
          }
        },
    },
    activated(){
      this.init();
    },
    watch:{
      QuestionList:{
        handler(){
          for(let i = 0;i<this.QuestionList.questions.length;i++){
            this.QuestionList.questions[i].serialNumber = i+1;
            if(this.QuestionList.questions[i].choices.length != 0){
              for(let k = 0;k<this.QuestionList.questions[i].choices.length;k++){
                this.QuestionList.questions[i].choices[k].serialNumber = k+1;
                  }
            }
          }
        localStorage.setItem("XSurveyInformation",JSON.stringify(this.QuestionList));
        //this.QuestionList = JSON.parse(localStorage.getItem("XSurveyInformation"));这会死循环！
      },
      deep: true}
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll);     //监听滚动条！
    },
}
</script>


<style scoped lang="scss">
    .isFixed{
      position:fixed;
      top:0;
      z-index:999;
    }
    #QuestionnareEditing{
      background-color:rgba(128, 133, 144, 0.72);
    }
    .el-menu:not(.el-menu--collapse){
        width:200px;
        min-height:400px;
    }
    .el-input{
        margin:8px 0;
    }
    .el-header,.el-main,.el-footer{
        //margin:20px 0;
        height:100%!important;
    }
    .el-header{
        .el-card{
            margin:4px 0 20px 0;
        }
    }
    .el-main{
      min-height: 200px;
      //background: rgba(128, 133, 144, 0.56);
      padding:0 20px!important;
    }
    .sharebutton{

    }
    .el-card{
        margin:20px 0;
        padding:0;
    }
    .Istoggle{
        
        height:26px;
        color:white;
        line-height: 30px;
        text-align: left;
        i{
            background:#409eff;
            width:55px;
            padding:5px;
            text-align: center;
        }
        
    }
    .el-icon-arrow-down{
        width:193px!important;
    }

</style>


