<template>
  <div id="job-table">
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      <!-- <ul class="title">
        <li class="list_title" style="width:9%;">
          <a>职位</a>
        </li>
        <li class="list_title" style="width:19%;">
          <a>公司名称</a>
        </li>
        <li class="list_title" style="width:29%;">
          <a>岗位要求</a>
        </li>
        <li class="list_title" style="width:9%;">
          <a>薪资</a>
        </li>
        <li class="list_title" style="width:19%;">
          <a>工作地点</a>
        </li>
        <li class="list_title" style="width:9%;">
          <a>发布时间</a>
        </li>
      </ul>
      <ul class="content" v-for="(item,index) in items.slice((currentPage-1)*pageSize,currentPage*pageSize)" @click="selectItem('/talent/talent-apply')" :key="index">
        <li style="width:9%;"><a>{{item.positionName}}</a></li>
        <li style="width:19%;"><a>{{item.companyName}}</a></li>
        <li style="width:29%;"><a>{{item.degreeRequired}}</a></li>
        <li style="width:9%;"><a>{{item.salary}}</a></li>
        <li style="width:19%;"><a>{{item.areas}}</a></li>
        <li style="width:9%;"><a>{{item.createTime}}</a></li>
      </ul> -->
      <!--列表-->
      <el-table @row-click="selectItem" :data="items.slice((currentPage-1)*pageSize,currentPage*pageSize)" highlight-current-row style="width: 100%; margin-bottom:2px;">
        <el-table-column width="10"></el-table-column>
        <el-table-column type="index" width="40"></el-table-column>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="[简介]">
                <div v-html="props.row.companyIntroduction" style="background:#e1e5f0;"></div>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="positionName" label="职位" width="250" sortable></el-table-column>
        <el-table-column prop="companyName" label="公司" width="250" sortable></el-table-column>
        <el-table-column prop="degreeRequired" label="职位要求" width="150" sortable></el-table-column>
        <el-table-column prop="salary" label="薪资" width="150" sortable></el-table-column>
        <el-table-column prop="areas" label="公司地点" width="150" sortable></el-table-column>
        <el-table-column prop="createTime" label="日期" width="150" sortable></el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination style="float:right;margin:10px" background layout="prev, pager, next" :total="parseInt(total)" :page-size="pageSize" :current-page.sync="currentPage">
        </el-pagination>
      </el-col>
    </el-col>
  </div>
</template>

<script>
export default {
  name: "job-table",
  props: ["data", "isLoading", "isRequested"],
  data() {
    return {
      items: [
        // {
        //   id: 0,
        //   positionName: "高级机械工程师", //职业
        //   companyName: "深圳库卡自动化设备有限公司", //公司
        //   degreeRequired:
        //     "1、项目前期验证，方案制作及报价核算；2、完成项目机械部分的设计;3、完成2D工程图及标准", //要求
        //   workAge: "3年以上",
        //   salary: "12k-25k", //薪资
        //   hiring: 1, //需求
        //   companyIntroduction: "暂无信息",
        //   phone: 123123123, //电话
        //   areas: "深圳",
        //   createTime: "2018-08-31"
        // }
      ],
      total: 0, // 总条目数
      pageSize: 15, // 每页显示的数量
      currentPage: 1, //当前页
      loading: false
    };
  },
  methods: {
    selectItem: function(row) {
      console.log(row)
      let path = '/talent/talent-apply';
      this.$emit("select", path,row.id);
    },
    init() {
      let that = this;
      console.log(that.data);
      if (that.data && that.data.list) {
        that.items = that.data.list;
        that.data.list.forEach((item, index) => {
          that.items[index].createTime = item.createTime.slice(0, 10);
        });
        that.loading = that.isLoading;
        that.total = that.data.total;
        that.pageSize = that.data.pageSize;
        setTimeout(function() {
          //显示跳出页面
          that.loading = false;
        }, 1000);
      }
    }
  },
  activated() {
    this.init();
  },
  watch: {
    isRequested() {
      this.init();
    }
  }
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#job-table {
  @include whole;
}
.active {
  color: #ed1c22 !important;
  border-bottom: 2px solid #ed1c22;
  padding-bottom: 4px;
}
.title {
  & {
    background: #7b7b7b;
    width: 100%;
  }
  li {
    & {
      display: inline-block;
    }
    a {
      color: #fff;
      cursor: default;
      font-size: 1em;
      font-weight: 500;
      line-height: 25px;
      vertical-align: middle;
      text-align: center;
      width: 100%;
      display: inline-block;
      box-sizing: border-box;
    }
  }
}
.content {
  & {
    width: 100%;
    margin: 5px 0;
  }
  &:focus a,
  &:hover a {
    white-space: initial;
    font-weight: 600;
  }
  li {
    & {
      display: inline-block;
    }
    a {
      font-size: 1em;
      color: #555555;
      font-weight: 500;
      line-height: 25px;
      vertical-align: middle;
      text-align: center;
      width: 100%;
      display: inline-block;
      box-sizing: border-box;
      max-height: 130px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: initial;
      // white-space: nowrap;
    }
  }
}
</style>



