/*问卷相关的接口函数调用*/

export default {
  //添加问卷发请求
  addSurvey: (tha,params) =>{
    let data;
       tha.axios.post(tha.HOST + "/survey/manager/addSurvey",params).then((response)=>{
        console.log(response.data);
        tha.isLoading = true;
              setTimeout(() => {
                tha.isLoading = false;
                if(response.data.result == "success"){
                  tha.$message({
                    message: '试卷创建成功！',
                    type: 'success'
                  });
                }else if(response.data.result == "error"){
                  tha.$message({
                    message: '试卷创建失败！页面编写逻辑出现问题！请及时联系管理员！',
                    type: 'error'
                  });
                }
                
        }, 2000);
        
        
      }).catch((response)=>{
        console.log(response.data);
        tha.isLoading = true;
        setTimeout(() => {
          tha.isLoading = false;
          tha.$message({
            message: '试卷创建失败！网络抽风了！',
            type: 'error'
          });
        }, 2000);
        
      })
      return data;
  },
  //添加题目发请求
  addQuestion: (tha,params) =>{
    return tha.axios.post(tha.HOST + "/survey/manager/addQuestion",params).then((response)=>{
      console.log(response.data);
    }).catch((response)=>{
      console.log(response.data);
    })
},
  //添加选项发请求
  addChoice: (tha,params) =>{
    return tha.axios.post(tha.HOST + "/survey/manager/addChoice",params).then((response)=>{
      console.log(response.data);
    }).catch((response)=>{
      console.log(response.data);
    })
},
  //更新问卷发请求
  updateSurvey: (tha,params) =>{
    let data;
    tha.axios.post(tha.HOST + "/survey/manager/updateSurvey",params).then((response)=>{
      console.log(response.data);
        tha.isLoading = true;
              setTimeout(() => {
                tha.isLoading = false;
                if(response.data.result == "success"){
                  tha.$message({
                    message: '试卷数据获取成功！',
                    type: 'success'
                  });
                }else if(response.data.result == "error"){
                  tha.$message({
                    message: '试卷数据获取失败！页面编写逻辑出现问题！请及时联系管理员！',
                    type: 'error'
                  });
                }
                
        }, 2000);
    }).catch((response)=>{
      console.log(response.data);
      tha.isLoading = true;
      setTimeout(() => {
        tha.isLoading = false;
        tha.$message({
          message: '试卷数据获取失败！网络抽风了！',
          type: 'error'
        });
      }, 2000);
    })
    return data;
},
  //更新题目发请求
  updateQuestion: (tha,params) =>{
    return tha.axios.post(tha.HOST + "/survey/manager/updateQuestion",params).then((response)=>{
      console.log(response.data);
    }).catch((response)=>{
      console.log(response.data);
    })
},
//更新选项发请求
updateChoice: (tha,params) =>{
  return tha.axios.post(tha.HOST + "/survey/manager/updateChoice",params).then((response)=>{
    console.log(response.data);
  }).catch((response)=>{
    console.log(response.data);
  })
},
//删除问卷发请求
deleteSurvey: (tha,params) =>{
  return tha.axios.post(tha.HOST + "/survey/manager/deleteSurvey",params,{headers:{'Content-Type':'application/x-www-form-urlencoded'}}).then((response)=>{
    console.log(response.data);
    if(response.data.result == "success"){
      tha.$message({
        type:'success',
        message:'删除成功'
      })
    }else{
      tha.$message({
        type:'error',
        message:'删除失败！网页编写好像抽风了！'
      })
    }
    
  }).catch((response)=>{
    console.log(response.data);
    tha.$message({
      type:'error',
      message:'删除失败！网络好像抽风了！'
    })
  })
},
//删除题目发请求
deleteQuestion: (tha,params) =>{
  return tha.axios.post(tha.HOST + "/survey/manager/deleteQuestion",params).then((response)=>{
    console.log(response.data);
  }).catch((response)=>{
    console.log(response.data);
  })
},
//删除选项发请求
deleteChoice: (tha,params) =>{
   tha.axios.post(tha.HOST + "/survey/manager/deleteChoice",params).then((response)=>{
    console.log(response.data);
    tha.$message({
      type:'success',
      message:'删除成功'
    })
  }).catch((response)=>{
    console.log(response.data);
    tha.$message({
      type:'error',
      message:'删除失败！网络好像抽风了！'
    })
  })
},
//搜索问卷发请求
search: (tha,params) =>{ 
  let data;
  tha.FormSort.DataSort = [];   //将日期筛选列表的格式赋空！
  const loading = tha.$loading({
    lock: true,
    text: '全舰数据装填中...',
    spinner: 'el-icon-loading',
    background: 'rgba(0, 0, 0, 0.7)'
  });
    tha.axios.post(tha.HOST + "/survey/manager/search",params).then((response)=>{
      console.log(response.data);
      data = response.data;
      tha.tableData = response.data;
      let OldData;
      for(let i = 0;i<tha.tableData.message.list.length;i++){
        tha.tableData.message.list[i].createTime =tha.tableData.message.list[i].createTime.split(" ")[0];
        if(tha.tableData.message.list[i].updateTime != null){
          tha.tableData.message.list[i].updateTime =tha.tableData.message.list[i].updateTime.split(" ")[0];
        }; 
        if(!OldData){
          OldData = tha.tableData.message.list[i].createTime;
          tha.FormSort.DataSort.push({
            text:tha.tableData.message.list[i].createTime,
            value:tha.tableData.message.list[i].createTime,
          })
        }
        if(OldData != tha.tableData.message.list[i].createTime&&OldData){
          tha.FormSort.DataSort.push({
            text:tha.tableData.message.list[i].createTime,
            value:tha.tableData.message.list[i].createTime,
          })
        }
        OldData = tha.tableData.message.list[i].createTime;
        if(tha.tableData.message.list[i].templateType == 0){
          tha.tableData.message.list[i].templateType = "问卷";
        }else if(tha.tableData.message.list[i].templateType == 1){
          tha.tableData.message.list[i].templateType ="模板";
        }
        if(tha.tableData.message.list[i].status == 0){
          tha.tableData.message.list[i].status = "已发布";
        }else if(tha.tableData.message.list[i].status == 1){
          tha.tableData.message.list[i].status ="已保存";
        }
      }
      setTimeout(()=>{
        loading.close();
      },1000);
    }).catch((response)=>{
      console.log(response.data);
      tha.$message({
        type:'error',
        message:'请求数据失败！网络好像抽风了！'
      })
    })

  return data;
},
//获取问卷详情
getSurveyInfo:(tha,params) =>{
   tha.axios.get(tha.HOST + "/survey/getSurveyInfo",{params:params}).then((response)=>{
    console.log(response.data);
    tha.items = response.data.message;
    localStorage.setItem("XSurveyInformation",JSON.stringify(tha.items));
    for(var i = 0;i<response.data.message.questions.length;i++){   //让数组按序号排列！
      if(response.data.message.questions[i].answerType == 2&&response.data.message.questions[i].choices.length == 0){  //如果这个问题是单项填空题的话
        tha.AnswerList.push({
          "questionId":response.data.message.questions[i].id,
          "content":""
        })
      };
      if(response.data.message.questions[i].answerType == 2&&response.data.message.questions[i].choices.length != 0){  //如果这个问题是多项填空题的话
        tha.AnswerList.push({
          "choiceId":"signal",    //用来将来删除的时候方便
          "content":""
        })
        for(var m = 0;m<response.data.message.questions[i].choices.length;m++){
          tha.AnswerSpace.push({
            "choiceId":response.data.message.questions[i].choices[m].id,      //放止漏选
            "content":""
          })  
        }
      };
      if(response.data.message.questions[i].answerType == 3){  //如果这个问题是打分题的话
        tha.AnswerList.push({
          "questionId":response.data.message.questions[i].id,
          "content":""
        })
      }
      if(response.data.message.questions[i].answerType == 0){  //如果这个问题是单项选择题的话
        tha.AnswerList.push({
          "choiceId":response.data.message.questions[i].choices[0].id,   //放止漏选
        })
      };
      if(response.data.message.questions[i].answerType == 1){  //如果这个问题是多项选择题的话
        tha.AnswerList.push({
          "choiceId":"signal",   //用来将来删除的时候方便
        });
      }
      
    };
    
  }).catch((response)=>{
    console.log(response.data);
  })
},
//回答问题
addAnswer:(tha,params) =>{
  return tha.axios.post(tha.HOST + "/survey/addAnswer",params).then((response)=>{
    console.log(response.data);
    if(response.data.result == "success"){
      tha.$message({
        type: 'success',
        message: '答案提交成功!'
      });
    }else if(response.data.result == "error"){
      tha.$message({
        type: 'error',
        message: '网页逻辑编写错误，请及时联系管理员解决!'
      });
    }
  }).catch((response)=>{
    console.log(response.data);
    tha.$message({
      type: 'error',
      message: '网络抽风了!'
    });
  })
},

};


