<template>
  <div id="exhibition-default">
    <div class="left_block">
      <!-- 位置路径 -->
      <div class="box1">
        <div class="curr_position">
          当前位置：
          <a @click="selectItem('/home')">服务平台</a>
          <span>></span>
          <a @click="selectItem('/exhibition')">展会</a>
        </div>
      </div>
      <!-- 展会动态 -->
      <!-- <div class="box2">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">展会动态</a>
          </li>
          <span class="more" @click="selectItem('')">-更多></span>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <meeting-dynamic></meeting-dynamic>
          </keep-alive>
        </div>
      </div> -->
      <!-- 即将举办 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">即将举办</a>
          </li>
        </ul>
        <div style="width:100%;">
          <meeting-item :sign="true" @displaySign="displaySign" style="margin-bottom:20px;" v-for="(item,index) in tabMessage[1].slice((currentPage[0]-1)*pageSize[0],currentPage[0]*pageSize[0])" :key="item.id" :my-message="tabMessage[1][index+(currentPage[0]-1)*pageSize[0]]" :current-tab="currentTab[1]"></meeting-item>
          <el-pagination background layout="prev, pager, next" :total="parseInt(total[0])" :page-size="pageSize[0]" :current-page.sync="currentPage[0]">
          </el-pagination>
        </div>
      </div>
      <!-- 展会回顾 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">展会回顾</a>
          </li>
        </ul>
        <div style="width:100%;">
          <meeting-item :sign="false" style="margin-bottom:20px;" v-for="(item,index) in tabMessage[2].slice((currentPage[1]-1)*pageSize[1],currentPage[1]*pageSize[1])" :key="item.id" :my-message="tabMessage[2][index+(currentPage[1]-1)*pageSize[1]]" :current-tab="currentTab[2]"></meeting-item>
          <el-pagination background layout="prev, pager, next" :total="parseInt(total[1])" :page-size="pageSize[1]" :current-page.sync="currentPage[1]" @current-change="currentChange()">
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="right_block">
      <!-- 精彩展会   -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">精彩展会</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!-- 用户 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '用户'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[4].cpn" v-bind="$attrs" v-on="$listeners"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 活动 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '活动'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[5].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 广告招收 -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
    </div>
    <!-- 报名界面 -->
    <el-dialog title="报名" :visible.sync="signVisible" :close-on-click-modal="true" :modal-append-to-body="false" top="8vh" v-loading="loading" element-loading-text="拼命加载中">
      <el-form :model="signForm" label-width="100px" :rules="signFormRules" ref="signForm">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="signForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="gender">
          <el-input v-model="signForm.gender" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="phone">
          <el-input v-model="signForm.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="微信" prop="weChat">
          <el-input v-model="signForm.weChat"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="signForm.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="公司" prop="companyName">
          <el-input v-model="signForm.companyName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="position">
          <el-input v-model="signForm.position" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="公司地址" prop="companyAddress">
          <el-input v-model="signForm.companyAddress" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="signVisible = false">取消</el-button>
        <el-button type="primary" @click.native="signSubmit">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import NormalList from "@/components/common/normal-list";
import MeetingDynamic from "@/components/common/meeting-dynamic";
import MeetingItem from "@/components/common/meeting-item";
import Banning from "@/components/common/banning";
import Sign from "@/components/sign/user-login"
import Qs from "qs";

export default {
  name: "exhibition-default",
  data() {
    return {
      //各小标题块内容,传给子组件渲染页面内容
      tabMessage: [
        "",
        [
          {
            id: 1,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address1",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 2,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address2",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 3,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address3",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 4,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address4",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 5,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address5",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 6,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address6",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 7,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address7",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 8,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address8",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 9,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address9",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          }
        ],
        [
          {
            id: 1,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address1",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 2,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address2",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 3,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address3",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 4,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address4",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 5,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address5",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 6,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address6",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 7,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address7",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 8,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address8",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 9,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address9",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 10,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address123213",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          }
        ],
        "",
        ""
      ], //动态。即将举办、已经举办
      //标志性内容，传给子组件做导航判断请求接口判断
      currentTab: [
        {
          name: "展会",
          list: "展会动态",
          cpn: "meeting-dynamic"
        },
        {
          name: "展会",
          list: "即将举办",
          cpn: "meeting-item"
        },
        {
          name: "展会",
          list: "展会回顾",
          cpn: "meeting-item"
        },
        {
          name: "",
          list: "精彩展会",
          cpn: ""
        },
        {
          name: "用户",
          list: "",
          cpn: "sign",
          newsUrl: ""
        },
        {
          name: "活动",
          list: "",
          cpn: "normal-list",
          newsUrl: ""
        }
      ],
      tabs: [
        {
          name: "用户中心",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "sign", //子组件名
          content: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "订阅资讯",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "normal-list", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "我的交易",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "banning", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "近期活动",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "banning", //子组件名
          content: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "协会活动",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "banning", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "其他",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "banning", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        }
      ],
      //子组件开始渲染的标志
      isRequested: false,
      total: [9, 10], // 总条目数
      pageSize: [3, 5], // 每页显示的数量
      currentPage: [1, 1], //当前页
      // listFirst: 0
      signVisible:false,
      signForm:{
        conferenceId:'',
        name:"",
        gender:'',
        phone:'',
        weChat:'',
        email:'',
        companyName:'',
        position:'',
        companyAddress:""
      },
      signFormRules:{
        name:[{ required: true, message: "请输入姓名", trigger: "blur" }],
        gender:[{ required: true, message: "请输入性别", trigger: "blur" }],
        phone:[{ required: true, message: "请输入电话", trigger: "blur" }],
        weChat:[{ required: false, message: "请输入微信", trigger: "blur" }],
        email:[{ required: true, message: "请输入邮箱", trigger: "blur" }],
        companyName:[{ required: true, message: "请输入公司名", trigger: "blur" }],
        position:[{ required: false, message: "请输入地址", trigger: "blur" }],
        companyAddress:[{ required: false, message: "请输入公司地址", trigger: "blur" }],
      },
      loading:false
    };
  },
  // watch: {
  //   currentPage(oldV, newV) {
  //     console.log(oldV, newV);
  //   }
  // },
  methods: {
    // currentChange() {
    //   this.listFirst = (this.currentPage[1] - 1) * 5;
    //   console.log(this.listFirst);
    // },
    // routerLink(path) {
    //   this.$router.push(path); //路由跳转
    // },
    displaySign(id){
      this.signVisible=true;
      this.signForm.conferenceId=id;
    },
    selectItem(path, data) {
      let item = JSON.stringify(data);
      this.$emit("select", path, item);
    }, //将item通过select传给父组件

    tabLink(index, tocpn) {
      //更改高亮标签
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //解决methods中无法更改data数据的问题
        }
      }
      var t = 6;
      if (this.tabs[index].belong == "用户") t = 6;
      else if (this.tabs[index].belong == "活动") t = 7;

      this.currentTab[t] = this.tabs[index].tocpn;
      //更改小标题块内容
    },
    init() {
      var a = 0,
        b = 0;
      var url = this.HOST + "/conference/getRelevantConference";
      //即将举办
      this.axios
        .get(url, {
          params: {
            relevantId: 0
          }
        })
        .then(response => {
          console.log(response.data.result);
          //页面信息初始化
          this.tabMessage[1] = response.data.message;
          this.total[0] = this.tabMessage[1].length;
          a = 1;
          this.isRequested = true; //组件初始化监听
        })
        .catch(function(response) {
          console.log(response);
        });
      //展会回顾
      this.axios
        .get(url, {
          params: {
            relevantId: -1
          }
        })
        .then(response => {
          console.log(response.data.result);
          //页面信息初始化
          this.tabMessage[2] = response.data.message;
          this.total[1] = this.tabMessage[2].length;
          b = 1;
        })
        .catch(function(response) {
          console.log(response);
        });
      if (a && b) {
        this.isRequested = true; //组件初始化监听
      }
    },
    //报名提交
    signSubmit(){
      let that = this;
      let para = Object.assign({}, that.signForm);
      // console.log(para)
      if (para.name && para.gender && para.phone&&para.email&&para.companyName) {
        that.$confirm("确认提交吗", "报名", { type: "warning" }).then(() => {
          let url = that.HOST + "/conference/attend";
          let params = Qs.stringify({
            conferenceId:that.signForm.conferenceId,
            name:para.name,
            gender:para.gender,
            phone:para.phone,
            weChat:para.weChat,
            email:para.email,
            companyName:para.companyName,
            position:para.position,
            companyAddress:para.companyAddress
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "报名成功",
                  duration: 1500
                });
                that.signVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.signVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    }
  },
  components: { NormalList, MeetingDynamic, MeetingItem,Sign,
    Banning }
