<template>
  <div id="productHome">
    <div id="left">
        <div id="location">
            当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <span class=blue>产品</span>
        </div>
        <div id="productIntroduce">
            <div class="bigTitle">产品推荐</div>
            <div class="whiteBox" v-if="message">
                <ul id="productListHead">
                    <li class="productList selectable" v-for="(i,index) in message.productRecommend.slice(listFirst,listFirst + 5)" @click.stop="routerLinkName('productNews',i.id,'id','产品推荐','type')">{{i.title}}</li>
                </ul>
                <button class="pageButton" v-bind:class="{active:index==pageNum}" v-for="(i,index) in Math.ceil(message.productRecommend.length/6)" @click="introducePage(index)">{{index + 1}}</button>
            </div>
        </div>
        <div id="productNews" v-if="message">
            <div class="bigTitle">产品新闻<div class="more">更多</div>
            </div>
            <div v-for="(i,index) in picNews" class="picNews" @click.stop="routerLinkName('productNews',i.id,'id','产品新闻','type')">
                <img v-bind:src="i.img">
                <div class="newsText selectable">
                    {{i.title}}
                </div>
            </div>
            <div class="whiteBox">
                <ul class="sixLineUl">
                    <li v-for="(i,index) in message.productNews.slice(newsFirst,newsFirst+6)" class="smallLink selectable" @click.stop="routerLinkName('productNews',i.id,'id','产品新闻','type')">
                        {{i.title}}
                    </li>
                </ul>
                <div id="newsPageSelect">
                    <button class="pageButton" v-bind:class="{active:index==newsPage}" v-for="(i,index) in Math.ceil(message.productNews.length/6)" @click.stop="toNewsPage(index)">{{index + 1}}</button>
                </div>
            </div>
        </div>
        <div id="productStore">
            <div class="bigTitle">产品库</div>
            <div class="whiteBox" v-if="message">
                <div class="flexBox">
                    <div class="productFrame" v-for="(i,index) in message.productLibrary.slice(storeFirst,storeFirst + 8)">
                        <img class="productPic" v-bind:src="i.img">
                        <p class="productName">{{i.name}}</p>
                    </div>
                </div>
                <div id="storePageSelect">
                    <button class="pageButton" v-bind:class="{active:index==storePage}" v-for="(i,index) in Math.ceil(message.productLibrary.length/8)" @click.stop="toStorePage(index)">{{index + 1}}</button>
                </div>
            </div>
        </div>
    </div>
    <div id="right">
        <div id="productHot">
            <div class="bigTitle">热门产品
            </div>
            <div class="whiteBox">
                <router-link to="/product" v-for="i in productHot" class="smallLink" :key="i">{{i}}<br></router-link>
            </div>
        </div>
        <div id="productEvaluate">
            <div class="bigTitle">产品评测
                <div class="more">更多</div>
            </div>
            <div class="whiteBox flexbox">
                <ul v-if="message">
                    <li class="evaluateList selectable" v-for="(i,index) in message.productEvaluation" @click.stop="routerLinkName('productNews',i.id,'id','产品评测','type')">
                        {{i.title}}
                    </li>
                </ul>
            </div>
        </div>
    </div> 
  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";

export default {
  name: "productHome",
  data() {
    return {
        message:undefined,
        listFirst:0,
        params:undefined,
        picNews:undefined,
        pageNum:0,
        newsFirst:0,
        newsPage:0,
        productEvaluate:undefined,
        productHot:[],
        storeFirst:0,
        storePage:0,
    };
  },
  components: {
    MHeader
  },
  methods:{
    introducePage(num){
        this.listFirst=num*6;
        this.pageNum=num;
      },
    routerLinkName:function(theName,paramValue,paramName,param2Value,param2Name){
        this.params=this.params || new Object();
        this.params[paramName]=paramValue;
        if (param2Name) {
            this.params[param2Name]=param2Value;
        }
        this.params['message']=this.message;
        this.$router.push({name:theName,params:this.params});
    },
    toNewsPage(num){
        this.newsFirst=num*6;
        this.newsPage=num;
    },
    toStorePage(num){
        this.storeFirst=num*6;
        this.storePage=num;
    }
  },
  created:function(){
        if (!this.message){
            this.axios.get(this.HOST + '/common/getIndex')
            .then((res)=>{
                this.message=res.data.message;
                /* 将有图片的新闻放在最前面 */
                var num=0;
                for (var i=0;i<this.message.productNews.length;i+=1){
                    if (this.message.productNews[i].img){
                        var x=this.message.productNews.splice(i,1);
                        x=x[0];
                        this.message.productNews.unshift(x);
                        num+=1;
                    }
                }
                this.picNews=this.message.productNews.splice(0,num);
            })
        }
        /*
        if (!this.productEvaluate){
            this.axios.get(this.HOST + '/information/getEvaluateList',{
                params:{
                    pageNum:1,
                }
            })
            .then((res)=>{
                this.productEvaluate=res.data.message;
            })
        }
        */
  }
};
</script>

<style lang="scss" scoped>
*{
    margin:0;
    padding:0;
}
.flexBox{
    display:flex;
    flex-wrap:wrap;
    justify-content:flex-start;
}
.selectable{
    color:rgb(111,111,111)
}
.selectable:hover{
    cursor:pointer;
    color:#127de7;
}
.blue{
    color:#127de7;
}
.more{
    float:right;
    color:#127de7;
    cursor:pointer;
}
.pageButton{
    margin:0 3px;
    padding:2px 6px;
    border:1px solid black;
    outline:none;
    cursor:pointer;
}
.smallLink{
    height:30px;
    line-height:30px;
}
.whiteBox{
    background:white;
    padding:5px;
}
.sixLineUl{
    height:185px;
}
button.active{
    color:white;
    background:black;
}
@mixin border{
    margin:10px 0;
    padding:5px;
    max-width:100%;
    background:rgba(255,255,255,0.8);
}
div#left{
    float:left;
    width:710px;
}
div#right{
    width:480px;
    float:right;
}
div#location{
    @include border;
}
div#productHome{
    width:1200px;
    margin:0 auto;
}
div#productIntroduce,div#productStore,div#productNews{
    @include border;
}
div.bigTitle{
    margin-bottom:5px;
}
div.picNews{
    width:350px;
    display:inline-block;
    position:relative;
    
    background:white;
}
div.newsText{
    float:left;
    position:absolute;
    width:260px;
    top:50%;
    left:90px;
    transform:translateY(-50%);
    letter-spacing:2px;
    font-size:1.1rem;
}
div.picNews > img{
    float:left;
    width:75px;
    margin:5px;
}
ul#productListHead{
    display:flex;
    height:175px;
    flex-direction:column;
    justify-content:flex-start;
}
li.productList{
    height:35px;
    line-height:35px;
}
div.productFrame{
    width:150px;
    display:inline-block;
    margin:10px;
    margin-bottom:0;
}
img.productPic{
    width:110px;
    height:110px;
    margin:20px;
    margin-bottom:5px;
    margin-top:10px;
    background:gray;
}
p.productName{
    text-align:center;
}
/*   以下是右边  */
/*              */
/*   ↖光↑↑翼↗   */
/*   ←( ·A·)→   */
/*   ↙展↓↓开↘   */
/*              */
// wfc你好骚鸭
div#productHot{
    @include border;
}
div#productEvaluate{
    @include border;
}
li.evaluateList{
    margin-top:6px;
    margin-bottom:6px;
}
</style>



