<template>
  <div id="productNews">
    <div id="left">
        <div id="location">
        当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <router-link to="/product" class="selectable">产品</router-link> > <span class="blue">{{type}}</span>
        </div>
        <div class="leftOutBox" v-if="newsInfo">
            <div class="leftInBox">
                <h1 id="bigTitle">{{newsInfo.title}}</h1>
                <div id="information">
                    来源 : <span class="blue">{{newsInfo.source}}</span> | 阅读量： | <span>纠错</span> | <span>分享</span>
                </div>
            </div>
            <div id="mainText">
                <div class="textBox" v-html="newsInfo.content[page].content">
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import MHeader from '@/components/common/m-header'

export default {
    name: "productNews",
    components: {
        
    },
    data(){
        return{
            newsInfo:undefined,
            newsRelated:undefined,
            type:undefined,
            page:0,
        }
    },
    methods:{

    },
    created:function(){
        //let data = Qs.stringify({"id":this.$route.params.id});
        console.log('theParams',this.$route.params);
        var newsUrl;
        this.type=this.$route.params.type;
        if (this.$route.params.type=='产品评测'){
            this.type='产品评测';
            newsUrl='/information/getEvaluateInfo';
        };
        if (this.$route.params.type=='产品新闻'){
          this.type='产品新闻';
          newsUrl='/information/getProductNewsInfo';
        }
        if(this.$route.params.type=='产品推荐'){
            newsUrl='/information/getRecommendInfo';
        }
        //console.log('saysomething',newsUrl,' ',this.$route.params);
        this.axios.get(this.HOST + newsUrl,{
            params:{
                id:this.$route.params.id
            }
        }
        ).then((res)=>{
            //console.log(res.data.message.information.content[0])
            this.newsInfo=res.data.message.information;
            this.newsRelated=res.data.message.related;
        })
    }
}
</script>
<style lang="scss" scoped>
div#productNews{
  width:1200px;
  margin:0 auto;
}
div#productNews /deep/ img{
  max-width:650px;
}
*{
    padding:0;
    margin:0;
}
.blue{
    color:#127de7;
}
div#location{
  margin-top:10px;
  width:700px;
  padding:5px;
  padding-bottom:0;
  background:rgba(255,255,255,0.8);
}
div.leftOutBox{
    width:700px;
    padding:5px;
    background:rgba(255,255,255,0.8);
    margin-bottom:10px;
}
div.leftInBox{
    background:white;
    padding:5px;
}
h1#bigTitle{
    margin:1.5rem 0;
    text-align:center;
}
div#information{
    color:gray;
    position:relative;
    left:2rem;
}
div.textBox{
    padding:10px;
    background:white;
}
</style>



