<template><!--填空题的框-->
    <div id="Space" @mouseover="function(){isShow=true;}" @mouseout="function(){isShow=false;}">
        <el-row >
            <el-col :span="20" class="Space_choice">
                <el-input placeholder="选项" v-model="QuestionList.questions[beanPrev].choices[bean].title"></el-input>
            </el-col>
            <el-col :span="1" :offset="1" v-if="isShow">
                    <el-button type="text" class='fa fa-trash-o fa-2x' @click="deletion"></el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="20">
                <el-input :disabled="true"></el-input>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    props:{
        bean:{
            
        },
        optionList:{

        },
        isSend:{

        },
        QuestionList:{

        },
        beanPrev:{
            
        },
    },
    data(){
        return{
            isShow:false,
            questionListSingle:{
              "serialNumber":1,         //序号                        没有发送0过来
              "title":"选项内容",   //标题
              "type":1,                  //选项型:0、文字型:1
              "defaultStatus":0         //默认状态                      没选就发0
              },
            }
        },
    methods:{
        deletion(){  //删除单个选项项的函数
            this.$emit('deletive',this.bean);   //必须要通知父节点删除
        }
    },
    watch:{
        isSend:function(){
         /*  this.questionListSingle.serialNumber = this.bean+1; 
          this.optionList.choices.push(this.questionListSingle); */
        },
        QuestionList:function(){
            this.QuestionList.questions[this.beanPrev].choices[this.bean].serialNumber = this.bean + 1;
        }
    }
}
</script>
<style scoped lang='scss'>
#Space{
    height:110px;
}
.el-row{
    margin:12px 0;
}
.Space_choice{
    margin:8px 0;
    height:50px;
}
i{
   margin:8px;
}
</style>


