<template>
  <div id="meeting-default">
    <div class="left_block">
      <!-- 位置路径 -->
      <div class="box1">
        <div class="curr_position">
          当前位置：
          <a @click="selectItem('/home')">服务平台</a>
          <span>></span>
          <a @click="selectItem('/meeting')">会议</a>
        </div>
      </div>
      <!-- 会议动态 -->
      <div class="box2">
        <ul class="title">
          <li class="list_title">
            <a
              class="active"
              style="margin-left:15px;"
            >会议动态</a>
          </li>
          <span
            class="more"
            @click="selectItem('')"
          >-更多></span>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <meeting-dynamic></meeting-dynamic>
          </keep-alive>
        </div>
      </div>
      <!-- 即将举办 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a
              class="active"
              style="margin-left:15px;"
            >即将举办</a>
          </li>
        </ul>
        <div style="width:100%;">
          <meeting-item
            :sign="true"
            @displaySign="displaySign"
            style="margin-bottom:20px;"
            v-for="(item,index) in tabMessage[1].slice((currentPage[0]-1)*pageSize[0],currentPage[0]*pageSize[0])"
            :key="item.id"
            :my-message="tabMessage[1][index+(currentPage[0]-1)*pageSize[0]]"
            :current-tab="currentTab[1]"
          ></meeting-item>
          <el-pagination
            background
            layout="prev, pager, next"
            :total="parseInt(total[0])"
            :page-size="pageSize[0]"
            :current-page.sync="currentPage[0]"
          >
          </el-pagination>
        </div>
      </div>
      <!-- 会议回顾 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a
              class="active"
              style="margin-left:15px;"
            >会议回顾</a>
          </li>
        </ul>
        <div style="width:100%;">
          <meeting-item
            :sign="false"
            style="margin-bottom:20px;"
            v-for="(item,index) in tabMessage[2].slice((currentPage[1]-1)*pageSize[1],currentPage[1]*pageSize[1])"
            :key="item.id"
            :my-message="tabMessage[2][index+(currentPage[1]-1)*pageSize[1]]"
            :current-tab="currentTab[2]"
          ></meeting-item>
          <el-pagination
            background
            layout="prev, pager, next"
            :total="parseInt(total[1])"
            :page-size="pageSize[1]"
            :current-page.sync="currentPage[1]"
            @current-change="currentChange()"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="right_block">
      <!-- 热门PPT下载 -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a
              style="margin-left:15px;"
              class="active"
            >热门PPT下载</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!-- 用户 -->
      <div class="box4">
        <ul
          class="title"
          style="margin-left:15px;"
        >
          <li
            class="list_title"
            v-for="(tab,index) in tabs"
            v-if="tab.belong === '用户'"
            :key="tab.name"
            @mouseover="tabLink(index,tab.tocpn)"
          >
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[4].cpn"  v-bind="$attrs" v-on="$listeners"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 活动 -->
      <div class="box4">
        <ul
          class="title"
          style="margin-left:15px;"
        >
          <li
            class="list_title"
            v-for="(tab,index) in tabs"
            v-if="tab.belong === '活动'"
            :key="tab.name"
            @mouseover="tabLink(index,tab.tocpn)"
          >
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[5].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 广告招收 -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
    </div>
    <!-- 报名界面 -->
    <el-dialog
      title="报名"
      :visible.sync="signVisible"
      :close-on-click-modal="true"
      :modal-append-to-body="false"
      top="8vh"
      v-loading="loading"
      element-loading-text="拼命加载中"
    >
      <el-form
        :model="signForm"
        label-width="100px"
        :rules="signFormRules"
        ref="signForm"
      >
        <el-form-item
          label="姓名"
          prop="name"
        >
          <el-input
            v-model="signForm.name"
            auto-complete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="性别"
          prop="gender"
        >
          <el-input
            v-model="signForm.gender"
            auto-complete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="联系方式"
          prop="phone"
        >
          <el-input
            v-model="signForm.phone"
            auto-complete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="微信"
          prop="weChat"
        >
          <el-input v-model="signForm.weChat"></el-input>
        </el-form-item>
        <el-form-item
          label="邮箱"
          prop="email"
        >
          <el-input
            v-model="signForm.email"
            auto-complete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="公司"
          prop="companyName"
        >
          <el-input
            v-model="signForm.companyName"
            auto-complete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="地址"
          prop="position"
        >
          <el-input
            v-model="signForm.position"
            auto-complete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="公司地址"
          prop="companyAddress"
        >
          <el-input
            v-model="signForm.companyAddress"
            auto-complete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click.native="signVisible = false">取消</el-button>
        <el-button
          type="primary"
          @click.native="signSubmit"
        >提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
