            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
            id: 5,
            coverImg: null, //封面图片地址
            title: "会议标题",
            holdTime: "",
            address: "address5",
            host: "host", //举办方
            introduction: "message..." //会议介绍
          },
          {
