<template>
  <div id="enterpriseNews">
    <div id="left">
        <div id="location">
        当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <router-link to="/enterprise" class="selectable">企业</router-link> > <span class="blue">{{type}}</span>
        </div>
        <div class="leftOutBox" v-if="newsInfo">
            <div class="leftInBox">
                <h1 id="bigTitle">{{newsInfo.title}}</h1>
                <div id="information">
                    来源 : <span class="blue">{{newsInfo.source}}</span> | 阅读量： | <span>纠错</span> | <span>分享</span>
                </div>
            </div>
            <div id="mainText">
                <div class="textBox" v-html="newsInfo.content[page].content">
                </div>
            </div>
        </div>
    </div>
    <enterprise-right></enterprise-right>
  </div>
</template>

<script>
import MHeader from '@/components/common/m-header'
import enterpriseRight from "@/components/enterprise/enterpriseRight";
import Qs from 'qs'

export default {
    name: "enterpriseNews",
    components: {
        enterpriseRight
    },
    data(){
        return{
            newsInfo:undefined,
            newsRelated:undefined,
            type:undefined,
            page:0,
        }
    },
    methods:{

    },
    created:function(){
        //let data = Qs.stringify({"id":this.$route.params.id});
        var newsUrl;
        this.type=this.$route.params.type;
        if (this.$route.params.type=='企业新闻'){
            newsUrl='/information/getCompanyNewsInfo';
        };
        if (this.$route.params.type=='会员动态'){
            newsUrl='/information/getCompanyNewsInfo';
        }
        console.log('saysomething',newsUrl,' ',this.$route.params);
        this.axios.get(this.HOST + newsUrl,{
            params:{
                id:this.$route.params.id
            }
        }
        ).then((res)=>{
            //console.log("result",res.data.message);
            console.log(res.data.message.information.content[0])
            this.newsInfo=res.data.message.information;
            this.newsRelated=res.data.message.related;
        })
    }
}
</script>
<style lang="scss" scoped>
div#enterpriseNews /deep/ img{
    max-width:650px;
}
*{
    padding:0;
    margin:0;
}
.blue{
    color:#127de7;
}
div.leftOutBox{
    width:700px;
    padding:5px;
    background:rgba(255,255,255,0.8);
    margin-bottom:10px;
}
div.leftInBox{
    background:white;
    padding:5px;
}
h1#bigTitle{
    margin:1.5rem 0;
    text-align:center;
}
div#information{
    color:gray;
    position:relative;
    left:2rem;
}
div.textBox{
    padding:10px;
    background:white;
}
</style>



