<template><!--选择题的选项-->
    <div id="Questionoption" @mouseover="function(){isShow=true;}" @mouseout="function(){isShow=false;}">
        <template>
            <el-row >
<!--                 <el-col :span="1">
                    <i class="fa fa-circle-o option_icon fa-2x"></i>
                </el-col> -->
                <el-col :span="20">
                    <el-input size="medium" placeholder="选项内容" clearable autosize class="option_input" prefix-icon="fa fa-circle-o option_icon" v-model="QuestionList.questions[beanPrev].choices[bean].title"></el-input>
                </el-col>
                <el-col :span="1" :offset="1" v-if="isShow">
                    <el-button class='fa fa-trash-o fa-2x option_icon' type="text" @click="deletion()"></el-button>
                </el-col>
            </el-row>
        </template>
    </div>
</template>
<script>
export default {
    props:{
        bean:{
            
        },
        optionList:{

        },
        isSend:{

        },
        QuestionList:{

        },
        beanPrev:{
            
        },
    },
        
    
    data(){
        return{
            isShow:false,
            questionListSingle:{
             "serialNumber":1,         //序号                        没有发送0过来
             "title":"此处应有选项内容",   //标题
             "type":0,                  //选项型:0、文字型:1
             "defaultStatus":0         //默认状态                      没选就发0
             },
        }
    },
    methods:{
        handledelete(){
            isShow=true;
        },
        deletion(){  //删除单个选项项的函数
            this.$emit('deletive',this.bean);   //必须要通知父节点删除
        }
    },
    watch:{
        isSend:function(){
            //this.questionListSingle.serialNumber = this.bean+1; 
            //this.optionList.choices.push(this.questionListSingle);
        },
        QuestionList:function(){
            this.QuestionList.questions[this.beanPrev].choices[this.bean].serialNumber = this.bean + 1;
        }

    }
    
}
</script>
<style scoped lang='scss'>
#Questionoption{
    height:50px;
}
.option_icon{
    display:inline-block;
    height: 100%;;
    margin-top:-13px;
}
.option_input{
    display: inline-block;
}
.el-row{
        margin:12px 0;
}
i{
    width:50px;
}
</style>


